<template>
	<div class="template-mine">
		<!--我的左右布局容器-专用-->
		<div class="mine-side">
			<ul class="mine-ul" v-if="data.length>0">
				<li :class="['mine-li', {'active': item.name===curpage}]" v-for="(item, index) in data" :key="item.id" @click="navPage(index)">{{item.name}}</li>
				<li :class="['mine-li', {'active': curpage==='退出当前登录'}]" @click="quit">退出当前登录</li>
			</ul>
		</div>
		<div class="mine-content">
			<slot></slot>
		</div>
	</div>
</template>

<script>
	import api from '@/api/api'
	import Storage from '@/util/storage'
	export default{
		props: ['curpage'],
		data() {
			return{
				data: [{
					id: 1,
					name: '个人资料',
					alias: 'profile',//别名
					page: '/profile'
				},{
					id: 2,
					name: '估分查询记录',
					alias: 'esrecord',//别名
					page: '/estimaterecords'
				},{
					id: 3,
					name: '选学校查询记录',
					alias: 'screcord',//别名
					page: '/schoolrecords'
				},{
					id: 4,
					name: '院校专业查询记录',
					alias: 'marecord',//别名
					page: '/majorrecords'
				},{
					id: 5,
					name: '预约咨询记录',
					alias: 'aprecord',//别名
					page: '/apporecordsclient'
				},
        //   {
				// 	id: 6,
				// 	name: '消息中心',
				// 	alias: 'message',//别名
				// 	page: '/messagecenter'
				// },
          {
					id: 7,
					name: '关于我们',
					alias: 'about',//别名
					page: '/about'
				},{
					id: 8,
					name: '设置',
					alias: 'setting',//别名
					page: '/setting'
				}]
			}
		},
		methods: {
			navPage(index) {
				var _this = this
				_this.$router.push({path: _this.data[index].page})
			},
			quit() {
				var _this = this
				api.requestlogout().then((res) => {
					return res.data
				}).then((res) => {
					if(res.code === 1 || res.code === 1){
						Storage.localRemove('user_id')
						_this.$router.push({
							path: 'index'
						})
					}else{
						_this.$message.warning(res.msg)
					}
				})
			}
		}
	}
</script>

<style scoped>
.template-mine{
	width: 1200px;
	margin: 24px auto 30px;
	font-size: 0;
	line-height: 0;
	letter-spacing: -4px;
}
.mine-side{
	display: inline-block;
	width: 233px;
	font-size: 0;
	line-height: 0;
	letter-spacing: -4px;
	vertical-align: top;
}
.mine-side::after{
	content: '';
	width: 13px;
	height: 100%;
	display: inline-block;
	vertical-align: top;
	background: none;
}
.mine-ul{
	width: 220px;
	letter-spacing: 0;
	display: inline-block;
	min-height: 634px;
	background: #FFFFFF;
}
.mine-li{
	width: 100%;
	height: 64px;
	padding: 0 36px;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	font-size: 18px;
	color: #666666;
	line-height: 64px;
	font-weight: 500;
	cursor: pointer;
}
.mine-li.active{
	color: #0084FF;
}
.mine-content{
	display: inline-block;
	width: 866px;
	min-height: 634px;
	background: #FFFFFF;
	vertical-align: top;
	letter-spacing: 0;
}
</style>



