	import CusPagetitle from '../components/pagetitle'
	import CusSinglecell from '../components/singlecell'
  import CusCard from '../components/cuscard'
  import CusCardPic from '../components/cuscardonlypic'
	import api from '../api/api'
	export default{
		created() {
			this.getrequestforecast()
		},
		data() {
			return {
				pageradiotxt: !!this.$route.query.type&&this.$route.query.type==='school'?'模拟选学校':'在线估分',
				historys: [],
				list: [{
					total: 496,
					chinese: 98,
					math: 99,
					english: 100,
					politics: 99,
				}],
				alldata: null,
				resultD: null,
				resultG: null,
				resultdata: null,
				recommenddatas: [{
					id: 1,
					title: '小太阳中小学培训中心',
          content: '此学校距您位置距离2.5km',
					img: require('../assets/images/home_1.png')
				},{
					id: 2,
					title: '小太阳中小学培训中心',
          content: '此学校距您位置距离2.5km',
					img: require('../assets/images/home_1.png')
				},{
					id: 3,
					title: '小太阳中小学培训中心',
          content: '此学校距您位置距离2.5km',
					img: require('../assets/images/home_1.png')
				},{
					id: 4,
					title: '小太阳中小学培训中心',
          content: '此学校距您位置距离2.5km',
					img: require('../assets/images/home_1.png')
				}]
			}
		},
		methods: {
			getrequestforecast() {
				var _this = this
				api.requestrecords({
					id: _this.$route.query.id || null
				}).then((res) => {
					return res.data
				}).then((res) => {
					// if(res.code === 1){
					  let _data = res
						_this.alldata = _data
						_this.resultD = _data.resultD
						_this.resultG = _data.resultG
						var renderitem = {
							msg: _data.scoreDmsg,
							result: _data.scoreD + '-' + _data.scoreG,
							subject: '总分'
						}
						for(var i=0; i<_data.resultD.length; i++){
							renderitem['msg'+i] = _data.resultD[i].msg
							renderitem['result'+i] = _data.resultD[i].result + '-' + _data.resultG[i].result
							renderitem['subject'+i] = _data.resultD[i].subject
						}
						_this.resultdata = [renderitem]
						// console.log(_this.resultdata)
						_this.historys = _data.result || []
					// }else{
					// 	_this.$message.error(res.msg);
					// }
				}).catch((err) => {
					console.log('err:', err)
				})
			}
		},
		components: {
			CusPagetitle,
			CusSinglecell,
			CusCard,
      CusCardPic
		}
	}
</script>

