<template>
	<div class="template-cusdialog">
		<!--核对信息提示-专用-->
		<el-dialog class="dialog-cusdialog" title="提示" center :visible.sync="isShowDialogVisible">
		  <div style="margin-top: -40px;">
		  	  <p class="cusdialog-msg">为保证查询结果的准确性，请仔细核对输入的信息是否有误</p>
		  </div>
		  <div slot="footer">
		  	  <div class="cusdialog-btns">
		  	  	<el-button class="cus-btn" type="primary" >去核对</el-button>
		  	  	<el-button class="cus-btn" type="primary" style="margin-left: 40px;" @click="success">已核对去查询</el-button>
		  	  </div>
		  </div>
		</el-dialog>
	</div>
</template>

<script>
	export default{
		props: ['paymoney', 'dialogvisible'],
		data() {
			return{
				isShowDialogVisible: this.dialogvisible || false,
			}
		},
    watch: {
      dialogvisible(val) {
        this.isShowDialogVisible = val
      }
    },
		methods: {
      success() {
        let _this = this
        _this.$emit('checkclose')
      }
		}
	}
</script>

<style scoped>
	.dialog-cusdialog{

	}
	.cusdialog-msg{
		font-size: 16px;
		color: #666666;
		line-height: 30px;
		text-align: center;
		font-weight: 500;
	}
	.cusdialog-agreement{
		width: 100%;
		font-size: 14px;
		color: #666666;
		line-height: 30px;
		font-weight: 500;
		text-align: left;
		margin-bottom: 20px;
	}
	.cusdialog-icon-select{
		font-size: 20px;
		vertical-align: middle;
		margin-right: 5px;
		cursor: pointer;
		height: 30px;
	}
	.cusdialog-btns{
		width: 100%;
		text-align: center;
		line-height: 0;
		font-size: 0;
	}
	.cus-btn{
		width: 200px;
		height: 44px;
	}
</style>



