<template>
	<div class="page-questions">
		<div class="questions">
			<cus-plate-title custitle="关于我们"></cus-plate-title>
			<div class="page-main">
				<el-row :gutter="26">
				  <el-col :span="6">
				    <el-menu
				      :default-active="defaultactive"
				      class="el-menu-vertical-demo"
				      @open="handleOpen"
				      @close="handleClose">
				      <el-menu-item class="sub-menu-1" index="1" @click="handlemenu('1')">
				        <span slot="title">企业简介</span>
				      </el-menu-item>
				      <el-menu-item class="sub-menu-1" index="2" @click="handlemenu('2')">
				        <span slot="title">企业文化</span>
				      </el-menu-item>
				    </el-menu>
				  </el-col>
				  <el-col :span="18">
				    <div class="menu-content-body">
              <div class="pt-details-content" v-html="data"></div>
            </div>
				  </el-col>
				</el-row>
			</div>
		</div>
	</div>
</template>

<script>
	import api from '@/api/api'
	import CusPlateTitle from '../components/cusplatetitle'
	export default{
		data() {
			return {
        defaultactive: '1',
        type: 'synopsis',
        data: ''
      }
		},
		watch: {
      $route(to, form) {
        this.type=to.query.type;
        return this.start()
      }
		},
    created() {
      this.type = this.$route.query.type||'synopsis'
      this.start()
    },
		methods: {
			handleOpen() {},
			handleClose() {},
      start() {
        this.requestaboutus()
      },
      requestaboutus() {
			  let _this = this
        api.requestaboutus({
          type: _this.type
        }).then(response=>{
          return response.data
        }).then(res=>{
          if(res.code === 1){
            _this.data = res.data
          }
        })
      },
      handlemenu(defaultactive) {
			  let _this = this
        _this.defaultactive = defaultactive
        switch (defaultactive) {
          case '1':
            _this.type = 'synopsis'
            break;
          case '2':
            _this.type = 'culture'
            break;
          default:
            break;
        }
        // _this.requestaboutus()
        _this.$router.push({path: '/aboutus', query: {type: _this.type}})
      }
		},
		components: {
			CusPlateTitle
		}
	}
</script>

<style scoped>
	.page-questions{
		width: 100%;
		background: #FFFFFF;
		padding-bottom: 40px;
	}
	.questions{
		width: 100%;
		min-height: 800px;
	}
	.page-main{
		width: 1200px;
		margin: 10px auto 0;
	}
	.sub-menu-1{
		margin-top: 20px;
		background: #F7F9FA;
		border: 1px solid #EEEEEE;
	}
	.menu-content-body{
		width: 100%;
    padding-top: 10px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
	}
	.menu-content-details{
    width: 100%;
    font-size: 14px;
    color: #666666;
    line-height: 20px;
    margin-bottom: 45px;
	}
</style>



