<style scoped>
	.page-appointment{
		width: 100%;
	}
	.appointment{
		width: 1200px;
		min-height: 800px;
		margin: 30px auto;
		background: #FFFFFF;
		border: 1px solid #EEEEEE;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.cus-radio-nav{
		width: 100%;
		padding: 0 20px;
