	.appointment-title{
		width: 100%;
		padding: 0 20px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		font-size: 26px;
		color: #0084FF;
		line-height: 40px;
