		position: absolute;
		left: 30px;
		top: 26px;
		z-index: 1;
		padding: 0;
		border: none;
	}
	.school-tip{
		width: 100%;
		font-size: 12px;
		color: #F40303;
		line-height: 20px;
		padding: 0 20px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
