<template>
	<div class="template-platetitle">
		<!--板块标题-->
		<div class="platetitle">
			<div class="plate-title-outer">
				<div class="plate-t-fix"></div>
				<h2 class="plate-title show-line-1">{{title}}</h2>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		props: ['custitle'],
		data() {
			return{
				title: this.custitle || null
			}
		},
    watch: {
      custitle(val) {
        this.title = val
      }
    }
	}
</script>

<style scoped>
	.template-platetitle{
		width: 100%;
	}
	.platetitle{
		width: 100%;
		height: 200px;
		background: url(../assets/images/listbg.png) no-repeat center/cover;
		padding-top: 55px;
		box-sizing: border-box;
	}
	.plate-title-outer{
		width: 356px;
		height: 90px;
		background: rgba(255, 255, 255, .6);
		position: relative;
		margin: 0 auto;

	}
	.plate-t-fix{
		width: 28px;
		height: 28px;
		background: #0084FF;
		position: absolute;
		left: -8px;
		top: -8px;
		z-index: 2;
	}
	.plate-title{
		width: 100%;
		height: 100%;
		padding: 0 10px;
		box-sizing: border-box;
		background: rgba(255, 255, 255, .6);
		position: absolute;
		top: 10px;
		left: 10px;
		font-size: 24px;
		color: #666666;
		line-height: 90px;
		text-align: center;
		font-weight: bold;
		letter-spacing: 6px;
	}
</style>



