<template>
	<div class="template-cusdialog">
		<!--支付方式-专用-->
		<transition name="el-fade-in-linear">
			<div class="cus-paymethod-mask" v-show="isShowDialogVisible">
				<div class="cus-message-box cus-message-box--center">
					<div class="cus-message-box__header">
						<div class="cus-message-box__title">
							<span>支付方式</span>
						</div>
						<button type="button" aria-label="Close" class="cus-message-box__headerbtn" @click="closeDialog">
							<i class="cus-message-box__close el-icon-close"></i>
						</button>
					</div>
					<div class="cus-message-box__content">
						<div class="cus-message-box__btns">
							<div class="cus-message-box__btn" @click="handlePay('alipay')">
								<img class="cus-message-box__btn-icon" src="../assets/images/zfb.png"/>
								<p class="cus-message-box__btn-txt">支付宝</p>
							</div>
							<div class="cus-message-box__btn" @click="handlePay('wechat')">
								<img class="cus-message-box__btn-icon" src="../assets/images/wx.png"/>
								<p class="cus-message-box__btn-txt">微信</p>
							</div>
						</div>
					</div>
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
	export default {
		props: ['dialogvisible'],
		data() {
			return {
				isShowDialogVisible: this.dialogvisible || false
			}
		},
		mounted(){
		    if(this.isShowDialogVisible){
				document.getElementsByTagName("body")[0].className="el-popup-parent--hidden";
			}else{
				document.body.removeAttribute("class","el-popup-parent--hidden");
			}
		},
    watch: {
      dialogvisible(val) {
        this.isShowDialogVisible = val
      }
    },
		methods: {
      closeDialog() {
				this.isShowDialogVisible = !this.isShowDialogVisible
				if(!!this.isShowDialogVisible){
					document.getElementsByTagName("body")[0].className="el-popup-parent--hidden";
				}else{
					document.body.removeAttribute("class","el-popup-parent--hidden");
				}
				this.$emit('closedialog')
			},
      handlePay(way) {
        let _this = this
        _this.$emit('handlepay', {
          way: way
        })
      }
		}
	}
</script>

<style scoped>
	.cus-paymethod-mask {
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		text-align: center;
		z-index: 2001;
		background: rgba(0,0,0,.5);
	}
	.cus-paymethod-mask::after {
		content: "";
		display: inline-block;
		height: 100%;
		width: 0;
		vertical-align: middle;
	}
	.cus-message-box {
		display: inline-block;
		width: 500px;
		padding-bottom: 10px;
		vertical-align: middle;
		background-color: #FFF;
		border-radius: 4px;
		border: 1px solid #EBEEF5;
		font-size: 18px;
		-webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
		box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
		text-align: left;
		overflow: hidden;
		-webkit-backface-visibility: hidden;
		backface-visibility: hidden;
	}

	.cus-message-box--center {
		padding-bottom: 30px;
	}

	.cus-message-box__header {
		position: relative;
		padding: 15px 15px 10px;
	}

	.cus-message-box--center .cus-message-box__header {
		padding-top: 30px;
	}

	.cus-message-box__title {
		padding-left: 0;
		margin-bottom: 0;
		font-size: 20px;
		color: #0084FF;
		line-height: 30px;
		font-weight: bold;
	}

	.cus-message-box--center .cus-message-box__title {
		position: relative;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
		-webkit-box-pack: center;
		-ms-flex-pack: center;
		justify-content: center;
	}
  .cus-message-box--center .cus-message-box__title>span{
    font-size: 20px;
  }

	.cus-message-box__headerbtn {
		position: absolute;
		top: 15px;
		right: 15px;
		padding: 0;
		border: none;
		outline: 0;
		background: 0 0;
		font-size: 16px;
		cursor: pointer;
	}
	.cus-message-box__content {
	    position: relative;
	    padding: 20px 15px;
	    font-size: 16px;
	    color: #666666;
	    line-height: 30px;
	    font-weight: bold;
	    text-align: center;
	}
	.cus-message-box--center .cus-message-box__btns{
		font-size: 0;
		line-height: 0;
		letter-spacing: -4px;
	}
	.cus-message-box__btn{
		display: inline-block;
		width: 230px;
		letter-spacing: 0;
		text-align: center;
	}
	.cus-message-box__btn-icon{
		width: 90px;
		height: 90px;
	}
	.cus-message-box__btn-txt{
		width: 100%;
		padding: 0 5px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		font-size: 20px;
		color: #666666;
		line-height: 30px;
		font-weight: 500;
		margin-top: 6px;
	}
</style>



