<template>
	<div class="page-profile">
		<div class="profile">
			<cus-mine-base curpage="个人资料">
				<div class="mine-content">
					<div class="avatar-view">
						<h4 class="mine-title">头像</h4>
						<div class="avatar-cover">
              <div class="avatar-uploader">
                <img v-if="!!imageUrl || imageUrl !== '' && imageUrl !== null" :src="baseURL + imageUrl" class="img-avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </div>
							<!--<el-upload
							  class="avatar-uploader"
							  :action="baseURL + '/api/index/upfile.html'"
							  :show-file-list="false"
							  name="image[]"
							  :on-success="handleAvatarSuccess"
							  :before-upload="beforeAvatarUpload">
							  <img v-if="!!imageUrl || imageUrl !== '' && imageUrl !== null" :src="baseURL + imageUrl" class="img-avatar">
							  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
							</el-upload>-->
						</div>
<!--						<el-button class="mine-btn" type="info">修改</el-button>-->
            <el-upload
              class="mine-btn"
              :action="baseURL + '/api/index/upfile.html'"
              :show-file-list="false"
              name="image[]"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload">
              修改
            </el-upload>
					</div>
					<div class="mine-option">
						<div class="txt-mine">昵称：{{nickname}}</div>
						<el-button class="mine-btn" type="info" @click="messageboxNicknameVisible=true">修改</el-button>
					</div>
					<div class="mine-option">
						<div class="txt-mine">当前学龄：{{grade}}</div>
						<el-button class="mine-btn" type="info" @click="messageboxgradeVisible=true">修改</el-button>
					</div>
				</div>
			</cus-mine-base>
		</div>
		<cus-messagebox title="修改学龄" :messageboxvisible="messageboxgradeVisible" @confirmdialog="updateGrade" @closedialog="closedialog">
			<el-select class="select-grade" v-model="levelval" placeholder="请选择学龄段">
		        <el-option v-for="item in gradedata" :key="'grade' + item.id" :label="item.name" :value="item.level"></el-option>
		    </el-select>
		</cus-messagebox>
		<cus-messagebox title="修改昵称" :messageboxvisible="messageboxNicknameVisible" @confirmdialog="updateNickname" @closedialog="closedialog">
			<el-input placeholder="请输入新的昵称" v-model="nicknameval" clearable></el-input>
		</cus-messagebox>
	</div>
</template>

<script>
	import api from '@/api/api'
	import {baseURL} from '../util/base'
	import CusMineBase from '../components/cusminebase'
	import CusMessagebox from '../components/cusmessagebox'
	export default{
		mounted() {
			//this.msgboxSaveSuccess()
			this.getGrade()
		},
		data() {
			return {
				nickname: '',
				grade: '',
				imageUrl: '',
        formgrade: {},
				nicknameval: this.nickname || '',
				levelval: null,
				// level_name: this.grade || '',
				messageboxgradeVisible: false,
				messageboxNicknameVisible: false,
				gradedata: []
			}
		},
		watch: {
			levelval(val,oldval){
				var _this = this
				var _grade = _this.gradedata.filter(function(obj, index){
					return obj.level === val
				})
        _this.formgrade = _grade[0]
			}
		},
    created() {
		  this.getData()
    },
		methods: {
			handleAvatarSuccess(res, file) {
	        	var _this = this
				//this.imageUrl = URL.createObjectURL(file.raw);

	            _this.updateAvatar(file)
			},
	        beforeAvatarUpload(file) {
	            const isJPG = file.type === 'image/jpeg' || file.type === 'image/png';
	            const isLt2M = file.size / 1024 / 1024 < 2;

	            if (!isJPG) {
	              this.$message.error('上传头像图片只能是 JPG/PNG格式!');
	            }
	            if (!isLt2M) {
	              this.$message.error('上传头像图片大小不能超过 2MB!');
	            }

	            return isJPG && isLt2M;
	        },
	        getData() {
	        	var _this = this
	        	api.requestcenter().then((res) => {
            return res.data
          }).then((res) => {
            if(res.code === 1){
              _this.nickname = res.data.username
              _this.imageUrl = res.data.avatar
              _this.levelval = res.data.grade
              _this.grade = res.data.grade_txt
            }
          }).catch((err) => {
            console.log('err:', err)
          })
            },
        getGrade() {
          var _this = this
          api.requestgetgrade().then((res) => {
            return res.data
          }).then((res) => {
            if(res.code === 1){
              _this.gradedata = res.data
            }
          }).catch((err) => {
            console.log('err',err)
          })
        },
        updateAvatar(files) {
          var _this = this
          api.requestprofile({
            avatar: files.response.data[0]
          }).then((res) => {
            return res.data
          }).then((res) => {
            if(res.code === 1){
              // _this.imageUrl = res.data[0]
              this.$message.success('头像修改成功')
              let timer = setTimeout(()=>{
                _this.getData()
                clearTimeout(timer)
              }, 1500)
            }else{
              this.$message.error(res.msg);
            }
          }).catch((err) => {
            console.log('err:', err)
          })
        },
        updateGrade() {
          var _this = this
          if(_this.levelval !== '' && _this.levelval !== null){
            api.requestprofile({
                  grade: _this.formgrade.level
                }).then((res) => {
              return res.data
            }).then((res) => {
              if(res.code === 1){
                _this.closedialog()
                _this.grade = _this.formgrade.name
                this.$message.success(res.msg);
              }else if(res.code === 401){
                _this.$message.error('请先登录!')
              }else{
                _this.$message.error(res.msg)
              }
            }).catch((err) => {
              console.log('err:', err)
            })
          }else{
            _this.$alert('由于您没有选择任何学龄，学龄将不会修改！')
          }
        },
        updateNickname() {
          var _this = this
          if(_this.nicknameval !== '' && _this.nicknameval !== null){
            api.requestprofile({
                  nickname: _this.nicknameval
                }).then((res) => {
              return res.data
            }).then((res) => {
              if(res.code === 1){
                _this.closedialog()
                _this.nickname = _this.nicknameval
              }
            }).catch((err) => {
              console.log('err:', err)
            })
          }else{
            this.$message({
                  message: '用户名不能为空!',
                  type: 'warning'
                });
          }
        },
        closedialog() {
          this.messageboxgradeVisible = this.messageboxNicknameVisible = false
        }
		},
		components: {
			CusMineBase,
			CusMessagebox
		}
	}
</script>

<style scoped>
	.page-profile{
		width: 100%;
	}
	.profile{
		width: 100%;
	}
	.mine-content{
		padding: 18px 24px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.avatar-view{
		width: 100%;
	}
	.mine-title{
		font-size: 16px;
		color: #666666;
		line-height: 30px;
	}
	.avatar-cover{
		width: 88px;
		height: 88px;
		margin: 6px 0;
	}
	.avatar-uploader{
		width: 100%;
		height: 100%;
		position: relative;
		overflow: hidden;
		cursor: pointer;
		border: 1px dashed #d9d9d9;
	}
	.avatar-uploader .el-upload:hover {
	    border-color: #409EFF;
	}
	.avatar-uploader-icon {
	    font-size: 28px;
	    color: #8c939d;
	    width: 88px;
	    height: 88px;
	    line-height: 88px;
	    text-align: center;
	}
	.img-avatar{
		width: 88px;
		height: 88px;
		display: block;
	}
	.mine-btn{
		width: 80px;
		height: 24px;
		background: #EEEEEE;
		border-radius: 4px;
		font-size: 14px;
		color: #999999;
		line-height: 24px;
		text-align: center;
		margin-top: 10px;
		border: none;
		padding: 0;
	}
	.mine-option{
		width: 100%;
		margin-top: 20px;
	}
	.txt-mine{
		width: 100%;
		font-size: 18px;
		color: #666666;
		line-height: 30px;
		font-weight: 500;
	}
</style>



