<template>
	<div class="page-school">
		<div class="school">
			<cus-pagetitle ptitle="选学校" pdesc="一键查询心仪学校历年录取分数线，方便快捷">
				<el-button type="info" class="cus-btn_info">返回上一页</el-button>
			</cus-pagetitle>
			<div class="form-school">
				<div class="form-row">
					生源地：
					<el-input
						class="input-school"
					  	placeholder=""
					    v-model="students">
					</el-input>
				</div>
				<div class="form-row">
					总分：
					<el-input
						class="input-school"
					  	placeholder=""
					    v-model="scores">
					</el-input>
				</div>
				<div class="form-row">
					位次：
					<el-input
						class="input-school"
					  	placeholder=""
					    v-model="rank">
					</el-input>
				</div>
				<div class="form-row" v-if="subjects.length>0">
					学科性质：
					<el-radio-group class="checkbox-school" v-model="checkedSubjects">
					    <el-radio v-for="item in subjects" :label="item.name" :key="item.id">{{item.name}}</el-radio>
					</el-radio-group>
				</div>
				<div class="form-row" v-if="batchs.length>0">
					批次：
					<el-checkbox-group class="checkbox-school" v-model="checkedBatchs">
					    <el-checkbox v-for="item in batchs" :label="item.name" :key="item.id">{{item.name}}</el-checkbox>
					</el-checkbox-group>
				</div>
				<div class="form-row">
					地域：
					<el-input
						class="input-school"
					  	placeholder=""
					    v-model="region">
					</el-input>
					<span style="font-size: 12px;color: #EE2929;line-height: 20px;font-weight: 500;margin-left: 10px;">*输入地域则会直接筛选该地域符合条件的学校，否则显示全部地区院校</span>
				</div>
			</div>
			<div class="school-result">
				<h2 class="school-title">估分结果</h2>
				<p class="school-tip">*单击任意学校信息即可查看该校近五年录取信息</p>
				<div class="result-body" style="width: 850px;">
					<cus-table :custh="custh" :listdata="listdata"></cus-table>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import CusPagetitle from '../components/pagetitle'
	import CusSinglecell from '../components/singlecell'
	import CusTable from '../components/custable'
	export default{
		created() {
			//this.msgboxSaveSuccess()
		},
		methods: {
			
		},
		data() {
			return {
				students: '',	//生源地
				scores: '',
				rank: '',
				subjects: [{
					id: 1,
					name: '文科'
				},{
					id: 2,
					name: '理科'
				}],
				checkedSubjects: [],
				batchs: [{
					id: 1,
					name: '本科一批'
				},{
					id: 2,
					name: '本科二批'
				},{
					id: 3,
					name: '专科'
				},{
					id: 4,
					name: '提前批'
				},{
					id: 5,
					name: '直招士官'
				},{
					id: 6,
					name: '地方专项'
				},{
					id: 7,
					name: '艺术类'
				}],
				checkedBatchs: [],
				region: '',
				custh: [{
					id: 1,
					value: '学院名称',
					props: 'college',
					isfilter: false
				},{
					id: 2,
					value: '学员代码',
					props: 'code',
					isfilter: false
				},{
					id: 3,
					value: '地域',
					props: 'region',
					isfilter: true
				},{
					id: 4,
					value: '院校性质',
					props: 'nature',
					isfilter: false
				}],
				listdata: [{
					id: 1,
					data: {
						college: '郑州大学',
						code: '00316',
						region: '河南',
						nature: '211'
					}
				},{
					id: 2,
					data: {
						college: '河南理工大学',
						code: '00156',
						region: '河南',
						nature: ''
					}
				},{
					id: 3,
					data: {
						college: '河南理工大学',
						code: '00156',
						region: '河南',
						nature: '双一流'
					}
				}]
			}
		},
		components: {
			CusPagetitle,
			CusSinglecell,
			CusTable
		}
	}
</script>

<style scoped>
	.page-school{
		width: 100%;
	}
	.school{
		width: 1200px;
		min-height: 800px;
		margin: 30px auto;
		background: #FFFFFF;
		border: 1px solid #EEEEEE;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.cus-btn_info{
		width: 100px;
		height: 26px;
		font-size: 16px;
		color: #999999;
		line-height: 1;
		background: #EEEEEE;
		border-radius: 4px;
		position: absolute;
		left: 30px;
		top: 26px;
		z-index: 1;
		padding: 0;
		border: none;
	}
	.form-school{
		width: 100%;
		padding: 5px 20px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		border-bottom: 1px solid #0084FF;
		margin-bottom: 20px;
	}
	.form-row{
		width: 100%;
		font-size: 18px;
		color: #666666;
		line-height: 20px;
		font-weight: 500;
		margin: 25px auto;
	}
	.input-school{
		width: 600px;
		height: 44px;
		font-size: 16px;
		color: #666666;
		line-height: 44px;
		vertical-align: text-top;
		vertical-align: middle;
	}
	.checkbox-school{
		display: inline-block;
	}
	hr{
	    height: 1px;
	    background: #BFBFBF;
	}
	.school-btns{
		width: 100%;
		padding: 0 20px;
		box-sizing: border-box;
		font-size: 0;
		line-height: 0;
		margin-bottom: 45px;
	}
	.school-btn{
		width: 200px;
		height: 44px;
		border-radius: 6px;
		font-size: 18px;
		color: #FFFFFF;
		line-height: 44px;
		text-align: center;
		font-weight: 500;
		box-sizing: border-box;
		padding-top: 0;
		padding-bottom: 0;
	}
	.btn-submit{
		background: #FACD89;
		border-color: #FACD89;
	}
	.btn-add{
		background: #F29B76;
		border-color: #FACD89;
	}
	.el-dialog__body{
		padding-top: 0 !important;
	}
	.school-title{
		width: 100%;
		padding: 0 20px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		font-size: 26px;
		color: #0084FF;
		line-height: 40px;
		font-weight: bold;
	}
	.school-tip{
		width: 100%;
		font-size: 12px;
		color: #F40303;
		line-height: 20px;
		padding: 0 20px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		margin-top: 20px;
	}
	.result-body{
		padding: 20px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
</style>


