<template>
	<div class="template-cusdialog">
		<!--支付-需要确认协议-->
		<el-dialog class="dialog-cusdialog" title="支付" center :visible.sync="isShowDialogVisible" :before-close="close">
		  <div style="margin-top: -40px;">
		  	  <p class="cusdialog-msg">首次预估分数需支付{{paymoney}}元</p>
		  </div>
		  <div slot="footer">
		  	  <p class="cusdialog-agreement">
			  	<i class="iconfont cusdialog-icon-select" :style="{color: isSelectedAgreement?'#0084FF':'#DCDCDC'}" @click="isSelectedAgreement=!isSelectedAgreement">&#xe61a;</i>
			  	<span>请阅读并同意《云智选服务协议》</span>
			  </p>
		  	  <div class="cusdialog-btns">
		  	  	<el-button class="cus-btn" type="info" @click="close">取消</el-button>
		  	  	<el-button class="cus-btn" type="primary" style="margin-left: 40px;" @click="confirmDialog">确定</el-button>
		  	  </div>
		  </div>
		</el-dialog>
	</div>
</template>

<script>
	export default{
		props: ['paymoney', 'dialogvisible'],
		watch: {
			dialogvisible(val, oldval){
				this.isShowDialogVisible = val
			}
		},
		data() {
			return{
				isShowDialogVisible: this.dialogvisible || false,
				isSelectedAgreement: false
			}
		},
		methods: {
			switchAgreement() {
				console.log(this.isSelectedAgreement)
				this.isSelectedAgreement = !this.isSelectedAgreement
			},
			confirmDialog() {
			  if(!!this.isSelectedAgreement){
          this.$emit('confirmdialog')
        }else{
			    this.$message.info('请确认已读协议并勾选!')
        }
			},
			close() {
				this.$emit('closedialog')
			}
		}
	}
</script>

<style scoped>
	.dialog-cusdialog{

	}
	.cusdialog-msg{
		font-size: 16px;
		color: #666666;
		line-height: 30px;
		text-align: center;
		font-weight: 500;
	}
	.cusdialog-agreement{
		width: 100%;
		font-size: 14px;
		color: #666666;
		line-height: 30px;
		font-weight: 500;
		text-align: left;
		margin-bottom: 20px;
	}
	.cusdialog-icon-select{
		font-size: 20px;
		vertical-align: middle;
		margin-right: 5px;
		cursor: pointer;
		height: 30px;
	}
	.cusdialog-btns{
		width: 100%;
		text-align: center;
		line-height: 0;
		font-size: 0;
	}
	.cus-btn{
		width: 200px;
		height: 44px;
	}
</style>



