const login = {
  namespaced: true,
  state: {
    userinfo: {}
  },
  getters: {
  	data(state, getters) {
        return state.data
    }
  },
  mutations: {
  	LOGIN(state, data) {
        state.userinfo = {...state.data,...data}
    }
  },
  actions: {
  	login({commit, state},data) {
        commit('LOGIN', data)
    }
  }
}
export default login



