<template>
	<div class="template-singlecell">
		<!--标签-值-按钮（按钮可选）-->
		<span class="singlecell-lab">{{title}}</span>
		<div class="singlecell-value">{{value}}</div>
		<slot class="slot-update" name="update"></slot>
	</div>
</template>

<script>
	export default{
		props: ['celltitle', 'cellvalue'],
		watch: {
			cellvalue(val, oldval){
				this.value = val
			}
		},
		data() {
			return{
				title: this.celltitle || '',
				value: this.cellvalue || ''
			}
		}
	}
</script>

<style scoped>
	.template-singlecell{
		display: inline-block;
		margin-right: 30px;
	}
	.singlecell-lab{
		display: inline-block;
		font-size: 16px;
		color: #666666;
		line-height: 30px;
		font-weight: 500;
		letter-spacing: 0;
		vertical-align: middle;
		/*padding: 12px 0;*/
    padding: 5px 0;
	}
	.singlecell-value{
		display: inline-block;
		width: 90px;
		height: 24px;
		font-size: 16px;
		color: #666666;
		line-height: 24px;
		text-align: center;
		font-weight: 500;
		border-radius: 4;
		border: 1px solid #E5E5E5;
		letter-spacing: 0;
		vertical-align: middle;
		cursor: pointer;
	}
	.slot-update{
		letter-spacing: 0;
	}
</style>



