<template>
	<div class="template-register">
		<el-dialog title="免费注册云智选" center :visible.sync="dialogFormVisible" :before-close="close">
		  <el-form class="cus-padding-30" :model="form" width="500px">
		    <el-form-item class="form-item" :class="[{'is-error': errinput==='nickname'}]">
		      <el-input v-model="form.nickname" auto-complete="off" placeholder="请输入昵称" clearable @input="errmsg='';errinput=''"></el-input>
		    </el-form-item>
		    <el-form-item class="form-item"
		    :rules="[
		      { required: true, message: '手机号不能为空'},
		      { type: 'number', message: '手机号必须为数字'}
		    ]" :class="[{'is-error': errinput==='mobile'}]">
		      <el-input v-model.number="form.mobile" auto-complete="off" placeholder="请输入11位手机号码" clearable maxlength="11" @input="errmsg='';errinput=''"></el-input>
		    </el-form-item>
		    <!--<el-form-item class="form-item" :class="[{'is-error': errinput==='imgcode'}]">
		      <el-input class="input-code" v-model="form.imgcode" auto-complete="off" placeholder="请输入图形验证码" clearable maxlength="6"></el-input>
		      <el-button class="get-code" @click="getImgcode">
		      	<img class="img-code" :src="srcimgcode"/>
		      </el-button>
		    </el-form-item>-->
		    <el-form-item class="form-item" :class="[{'is-error': errinput==='smscode'}]">
		      <el-input class="input-code" v-model="form.smscode" auto-complete="off" placeholder="请输入短信验证码" clearable maxlength="6" @input="errmsg='';errinput=''"></el-input>
		      <el-button class="get-code" :disabled="countdownlen===0?false:true" @click="getSMSCode">{{countdownlen===0?'获取验证码':countdownlen+'秒后重发'}}</el-button>
		    </el-form-item>
		    <el-form-item class="form-item" v-if="gradedata.length>0" :class="[{'is-error': errinput==='grade'}]">
		      <el-select class="select-grade" v-model="form.grade" placeholder="请选择学龄段">
		        <el-option v-for="(item, index) in gradedata" :key="index" :label="item.label" :value="item.value"></el-option>
		      </el-select>
		    </el-form-item>
		    <el-form-item class="form-item" :class="[{'is-error': errinput==='password'}]">
		      <el-input
			    placeholder="请输入登录密码"
			    v-model="form.password" :type="isPasswordShow?'tel':'password'">
			    <i slot="suffix" class="iconfont" style="padding: 0 5px;box-sizing: border-box;cursor: pointer;font-size: 24px;" @click.stop="switchPasswordShow">{{isPasswordShow?'&#xe60c;':'&#xe6c7;'}}</i>
			  </el-input>
		    </el-form-item>
		  </el-form>
		  <div class="cus-error cus-padding-30" style="width: 100%;font-size: 12px;color: #F56C6C;line-height: 30px;">{{errmsg}}</div>
		  <div class="check-agreement cus-padding-30" style="line-height: 30px;letter-spacing: 0;cursor: pointer;">
		  	<i v-if="!isCheckAgreement" class="iconfont" style="font-size: 16px;" @click.stop="checkAgreement">&#xe608;</i>
		  	<i v-else class="iconfont" style="font-size: 16px;color: #0084FF;border-color: #0084FF;" @click.stop="checkAgreement">&#xe6c9;</i>
		  	<span style="font-size: 14px;color: #666666;">阅读并同意</span>
		  	<a href="#" style="font-size: 14px;color: #0084FF;">《云智选服务协议》</a>
		  </div>
		  <div slot="footer" class="dialog-footer cus-padding-30">
		    <el-button type="primary" @click="register" style="width: 100%;font-size: 18px;font-weight: bold;letter-spacing: 6px;">立即注册</el-button>
		  </div>
		</el-dialog>
	</div>
</template>

<script>
	import api from '@/api/api'
	import {baseURL} from '@/util/base'
  import Storage from '@/util/storage'
	export default{
		props: ['dialogvisible'],
		watch: {
			dialogvisible(val) {
				this.dialogFormVisible = val
			}
		},
		mounted() {
			this.getgrade()
		},
		data() {
			return {
				dialogFormVisible: this.dialogvisible || false,
				initform: {
					nickname: '',
					mobile: '',
					// imgcode: '',
					smscode: '',
					grade: '',
					password: ''
				},
				form: {
					nickname: '',
					mobile: '',
					// imgcode: '',
					smscode: '',
					grade: '',
					password: ''
				},
				isPasswordShow: false,
				isCheckAgreement: false,
				errmsg: '',
				errinput: '',
				gradedata: [],
				// srcimgcode: this.imgcodeurl(),
				timer: null,	//定时器
				countdownlen: 0	//倒计时长
			}
		},
		methods: {
			switchPasswordShow() {
				this.isPasswordShow = !this.isPasswordShow
			},
			checkAgreement() {
				this.isCheckAgreement = !this.isCheckAgreement
			},
      imgcodeurl() {
        var num=new Date().getTime();
        Storage.localSet('imgcodetime', num)
        return baseURL + '/api/index/imgcode?time=' + num
      },
      getImgcode() {
        this.srcimgcode = this.imgcodeurl()
      },
			countdown() {
				var _this = this
				_this.countdownlen = 60
				_this.timer = setInterval(function(){
					if(_this.countdownlen>0){
						_this.countdownlen = _this.countdownlen - 1
					}else{
						clearInterval(_this.timer)
					}
				}, 1000)
			},
			codevalidate() {
				var _this = this
				var promise = new Promise(function(resolve, reject){
					if(_this.form.mobile !== '' && _this.form.mobile !== null){
						// if(_this.form.imgcode !== '' && _this.form.imgcode !== null){
							resolve()
						// }else{
						// 	_this.errmsg = '图形验证码不能为空'
						// 	_this.errinput = 'imgcode'
						// }
					}else{
						_this.errmsg = '手机号不能为空'
						_this.errinput = 'mobile'
					}
				})

				return promise
			},
			getSMSCode() {
				var _this = this
				_this.codevalidate().then(() => {
					api.requestgetsmscode({
						mobile: _this.form.mobile,
					}).then((res) => {
						return res.data
					}).then((res) => {
						if(res.code === 1){
							_this.countdown()
						}else{
							this.$message.error(res.msg);
						}
					}).catch((err) => {
						console.log('err:', err)
					})
				}).catch((err) => {
					console.log('err:', err)
				})
			},
			formvalidate() {
				var _this = this
				var promise = new Promise(function(resolve, reject){
					if(_this.form.nickname !== '' && _this.form.nickname !== null){
						if(_this.form.mobile !== '' && _this.form.mobile !== null){
							// if(_this.form.imgcode !== '' && _this.form.imgcode !== null){
								if(_this.form.smscode !== '' && _this.form.smscode !== null){
									if(_this.form.grade !== '' && _this.form.grade !== null){
										if(_this.form.password !== '' && _this.form.password !== null){
											if(!!_this.isCheckAgreement){
												resolve()
											}else{
												_this.$alert('请认真阅读用户协议并同意协议')
											}
										}else{
											_this.errmsg = '请设置密码'
											_this.errinput = 'password'
										}
									}else{
										_this.errmsg = '请选择学龄段'
										_this.errinput = 'grade'
									}
								}else{
									_this.errmsg = '短信验证码不能为空'
									_this.errinput = 'smscode'
								}
							// }else{
							// 	_this.errmsg = '图形验证码不能为空'
							// 	_this.errinput = 'imgcode'
							// }
						}else{
							_this.errmsg = '手机号不能为空'
							_this.errinput = 'mobile'
						}
					}else{
						_this.errmsg = '用户名不能为空'
						_this.errinput = 'nickname'
					}
				})

				return promise
			},
			register() {
				var _this = this
				_this.formvalidate().then(() => {
					api.requestregister({
						mobile: _this.form.mobile,
				        password: _this.form.password,
				        username: _this.form.nickname,
				        smscode: _this.form.smscode,
				        level: _this.form.grade
					}).then((res) => {
						return res.data
					}).then((res) => {
						if(res.code === 1){
              this.$message.error(res.msg);
							this.close({
								type: 'login',	//弹窗功能
								nextdialog: true	//是否打开弹窗
							})
						}else{
							this.$message.error(res.msg);
						}
					}).catch((err) => {
						console.log('err:', err)
					})
				}).catch((err) => {
					console.log('err:', err)
				})
			},
			getgrade() {
				var _this = this
				api.requestgetgrade().then((res) => {
					return res.data
				}).then((res) => {
					if(res.code === 1){
						_this.gradedata = res.data
					}
				}).catch((err) => {
					console.log('err',err)
				})
			},
			close(data) {
			  let _this = this
				if(this.timer !== null){
					_this.countdownlen = 0
					clearInterval(_this.timer)
				}
				if(data!==undefined){
					this.$emit('closedialog', data);//调用父组件的自定义事件，并传值
				}else{
					this.$emit('closedialog', {
						type: '',	//弹窗功能
						nextdialog: false	//是否打开弹窗
					});//调用父组件的自定义事件，并传值
				}
			}
		}
	}
</script>

<style scoped>
	/*.el-dialog{
		width: 500px;
	}
	.el-dialog__title {
	    line-height: 24px;
	    font-size: 20px;
	    color: #0084FF;
	    font-weight: bold;
	}
	.el-dialog--center .el-dialog__body {
	    text-align: initial;
	    padding: 50px 50px 0;
	}
	.el-input__inner{
		height: 44px;
		line-height: 44px;
	}*/
	/*.input-code{
		width: 245px;
	}
	.get-code{
		width: 140px;
		height: 44px;
		float: right;
	}
	.img-code{
		width: 100%;
		height: 100%;
	}
	.select-grade{
		width: 100%;
	}*/
	/*.el-select-dropdown__list{
		width: 400px;
		line-height: auto;
		font-size: 0;
		letter-spacing: -4px;
		padding: 10px 0;
	}
	.el-select-dropdown__item{
		display: inline-block;
		line-height: 30px;
		border: 1px solid #DCDCDC;
		letter-spacing: 0;
		margin: 10px 15px;
		padding: 0 15px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.el-dialog__headerbtn .el-dialog__close {
	    font-weight: bolder;
	}
	.el-dialog__footer {
	    padding: 10px 50px 50px;
	    text-align: right;
	}*/
</style>



