<template>
	<div class="template-pagetitle">
		<!--页面内容title部分-项目专用-->
		<div class="pagetitle-wrapper">
			<h1 class="pagetitle">{{title}}</h1>
			<p class="pagetitle-desc">{{desc}}</p>
			<slot></slot>
		</div>
	</div>
</template>

<script>
	export default{
		props: ['ptitle', 'pdesc'],
		data() {
			return{
				title: this.ptitle || '',
				desc: this.pdesc || ''
			}
		}
	}
</script>

<style scoped>
	.template-pagetitle{
		width: 100%;
		border-bottom: 1px solid #0084FF;
	}
	.pagetitle-wrapper{
		width: 100%;
		padding: 20px 10px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		text-align: center;
		position: relative;
	}
	.pagetitle{
		width: 100%;
		font-size: 32px;
		color: #0084FF;
		line-height: 50px;
		font-weight: bold;
	}
	.pagetitle-desc{
		font-size: 16px;
		color: #666666;
		line-height: 30px;
		font-weight: 500;
	}
</style>


