<template>
	<div class="page-companys">
		<div class="companys">
			<cus-plate-title :custitle="sectiondata.navitem.title"></cus-plate-title>
			<div class="page-main">
				<el-row :gutter="26">
				  <el-col :span="17">
				    <div class="menu-content-body" v-if="!!data.count&&data.count">
				    	<div class="menu-news-list" v-if="!!data.article&&data.article.length">
				    		<div class="menu-news-item" v-for="(item, index) in data.article" :key="index" @click="toDetails(item)">
				    			<p class="menu-news-content show-line-1"><i class="mn-list-style-disc"></i>{{item.title}}</p>
				    			<span class="menu-news-time">{{item.create_time_txt}}</span>
				    		</div>
				    	</div>
				    	<div class="menu-news-pager">
				    		<el-pagination
							  background
							  layout="pager, prev, next"
                :total="data.count"
                @current-change="handlePage"
							  prev-text="上一页"
							  next-text="下一页">
							</el-pagination>
				    	</div>
				    </div>
            <Nodatawithpic v-else></Nodatawithpic>
				  </el-col>
				  <el-col :span="7">
				    <div class="news-txt-card">
						<h3 class="news-card-title">公司动态<span class="news-tabs-more">更多<i class="el-icon-d-arrow-right news-tabs-more-icon"></i></span></h3>
						<div class="news-body">
							<div class="pt-tabs-card">
								<el-row :gutter="13">
									<el-col :span="8">
										<div class="pt-tabs-pic-poster"></div>
									</el-col>
									<el-col :span="8">
										<div class="pt-tabs-pic-poster"></div>
									</el-col>
									<el-col :span="8">
