									</el-col>
								</el-row>
							</div>
							<div class="news-tabs-txt-list">
								<ul class="news-tabs-ul">
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题标题标题标题标题标题标题标题</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题题标题标题</li>
								</ul>
							</div>
						</div>
					</div>
				    <div class="news-txt-card">
						<h3 class="news-card-title">公司动态<span class="news-tabs-more">更多<i class="el-icon-d-arrow-right news-tabs-more-icon"></i></span></h3>
						<div class="news-body">
							<div class="pt-tabs-card">
								<el-row :gutter="13">
									<el-col :span="8">
										<div class="pt-tabs-pic-poster"></div>
									</el-col>
									<el-col :span="8">
										<div class="pt-tabs-pic-poster"></div>
									</el-col>
									<el-col :span="8">
										<div class="pt-tabs-pic-poster"></div>
									</el-col>
								</el-row>
							</div>
							<div class="news-tabs-txt-list">
								<ul class="news-tabs-ul">
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题标题标题标题标题标题标题标题</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题题标题标题</li>
								</ul>
							</div>
						</div>
					</div>
				  </el-col>
				</el-row>
			</div>
		</div>
	</div>
</template>

<script>
  import api from '@/api/api'
  import CusPlateTitle from '../components/cusplatetitle'
  export default{
    data() {
      return {
        page: 1,
        data: {},
      }
    },
    components: {
      CusPlateTitle
    },
    /*beforeRouteUpdate(to,from,next){
      this.navigate_id=to.query.id;
      console.log('navigate_id----', this.navigate_id)
      next();
    },*/
    created() {
      // this.sectiondata = JSON.parse(this.$route.query.sectiondata)||{}
      this.start()
    },
    methods: {
      start() {
        this.requestArticlelist()
      },
      getSidebar() {
        let promise = new Promise((resolve, reject)=>{
          api.requestarticlenav({
            pid: this.$route.query.id
          }).then(response=>{
            return response.data
          }).then(res=>{
            if(res.code === 1){
              let data = res.data||[]
              this.$store.dispatch('articlenav/updatearticlenav', data)
              this.list[2]=data.map((obj, index)=>{
                let subdata = obj.son_nav.map((ob, ind)=>{
                  let subalias = ''
                  if(obj.son_nav.length>0){
                    subalias = 'questions'
                  }else{
                    subalias = 'companys'
                  }
                  return {
                    id: ob.id,
                    name: ob.title,
                    alias: subalias
                  }
                })
                let alias = ''
                if(obj.son_nav.length>0){
                  alias = 'questions'
                }else{
                  alias = 'companys'
                }
                return {
                  id: obj.id,
                  name: obj.title,
                  alias: alias,
                  son_nav: subdata
                }
              })
              resolve()
            }
          }).catch(err=>{})
        })
        return promise
      },
      requestArticlelist() {
        api.requestarticlelist({
          navigate_id: this.sectiondata.navdata[this.sectiondata.curtab].id,
          page: this.page,
          num: 16
        }).then(response=>{
          return response.data
        }).then(res=>{
          if(res.code === 1){
            this.data = res.data||{}
          }
        })
      },
      handlePage(currentPage) {
        this.page = currentPage
        this.requestArticlelist()
      },
      toDetails(item) {
        this.$router.push({path: '/newsdetails', query:{id: item.id}})
      }
    },
  }
</script>

<style scoped>
	.page-companys{
		width: 100%;
		background: #FFFFFF;
		padding-bottom: 40px;
	}
	.companys{
		width: 100%;
		min-height: 800px;
	}
	.page-main{
		width: 1200px;
		margin: 10px auto 0;
	}
	.sub-menu-1{
		margin-top: 20px;
		background: #F7F9FA;
		border: 1px solid #EEEEEE;
	}
	.menu-content-body{
		width: 100%;
	}
	.menu-news-pager{
		width: 100%;
		margin-top: 40px;
		text-align: center;
	}
</style>



