<template>
	<div class="template-card" v-if="listdatas.length>0">
		<!--上图下文-->
		<div class="cus-card-list">
			<div class="cus-card-item" v-for="item in listdatas" :key="item.id" :style="{width: !!cuswidth?(cuswidth - 0 + 14) + 'px' : '194px'}">
				<div class="cus-card-view" :style="{width: !!cuswidth?cuswidth + 'px' : '180px'}">
					<div class="cus-card-poster" :style="{height: !!cusheight?cusheight + 'px' : '90px'}">
						<img class="img-poster" :src="item.img"/>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		props: ['cuswidth', 'cusheight', 'listdata'],
		data() {
			return{
				listdatas: this.listdata || []
			}
		}
	}
</script>

<style scoped>
	.template-card{
		width: 100%;
    padding: 0 8px;
    box-sizing: border-box;
	}
	.cus-card-list{
		width: 100%;
		font-size: 0;
		line-height: 0;
		letter-spacing: -4px;
	}
	.cus-card-item{
		width: 272px;
		display: inline-block;
		letter-spacing: 0;
    vertical-align: top;
	}
	.cus-card-view{
		width: 242px;
		background: #FFFFFF;
		margin: 0 auto;
	}
	.cus-card-view::before,
	.cus-card-view::after{
		content: '';
		display: inline-block;
		width: 15px;
		height: 10px;
	}
	.cus-card-poster{
		width: 100%;
		height: 180px;
		position: relative;
		overflow: hidden;
	}
	.img-poster{
		width: auto;
		height: auto;
		min-width: 100%;
		min-height: 100%;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		z-index: 1;
	}
	.cus-card-title{
		width: 100%;
		font-size: 16px;
		color: #666666;
		line-height: 30px;
		font-weight: bold;
		margin: 10px auto;
	}
</style>



