<template>
	<div class="page-major">
		<div class="major">
			<cus-mine-base curpage="院校专业查询记录">
				<div class="mine-content">
					<ul class="mine-view-ul" v-if="data.length>0">
						<li class="mine-view-li" v-for="item in data" :key="item.id">
							<div class="mine-view-title show-line-1">{{item.title||'院校专业查询'}}</div>
							<div class="mine-view-time">{{item.time}}</div>
							<el-button class="mine-btn" type="info" @click="toDetails(item)">详情</el-button>
						</li>
					</ul>
					<!--<div v-else style="width: 100%;font-size: 18px;color: #999999;line-height: 30px;text-align: center;padding: 200px 0;font-weight: 500;-webkit-box-sizing: border-box;-moz-box-sizing: border-box;box-sizing: border-box;">暂无记录</div>-->
          <Nodatawithpic v-else></Nodatawithpic>
				</div>
			</cus-mine-base>
		</div>
	</div>
</template>

<script>
	import CusMineBase from '../components/cusminebase'
  import api from '../api/api'
	export default{
		data() {
			return {
			  data: [],
				datas: [{
					id: 1,
					title: '在线估分查询',
					time: '2019-08-12 11：52'
				},{
					id: 2,
					title: '在线估分查询',
					time: '2019-08-12 11：52'
				},{
					id: 3,
					title: '在线估分查询',
					time: '2019-08-12 11：52'
				},{
					id: 4,
					title: '在线估分查询',
					time: '2019-08-12 11：52'
				},{
					id: 5,
					title: '在线估分查询',
					time: '2019-08-12 11：52'
				}]
			}
		},
    created() {
      this.requestProfessionalRecord()
    },
    methods: {
      requestProfessionalRecord() {
        api.requestprofessionalrecord().then(response=>{
          return response.data
        }).then(res=>{
          if(res.code === 1){
            this.data = res.data||[]
          }
        }).catch(err=>{
          console.log('err', err)
        })
      },

      toDetails(item) {
        this.$router.push({path: '/majorresult', query: {id: item.id}})
      }
    },
		components: {
			CusMineBase
		}
	}
</script>

<style scoped>
	.page-major{
		width: 100%;
	}
	.major{
		width: 100%;
	}
	.mine-content{
		padding: 18px 24px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.mine-view-ul{
		width: 100%;
	}
	.mine-view-li{
		width: 100%;
		min-height: 60px;
		font-size: 0;
		line-height: 0;
		letter-spacing: -4px;
		border-bottom: 1px dashed #BBBBBB;
	}
	.mine-view-li::after{
		content: '';
		display: inline-block;
		width: 0;
		height: 60px;
		vertical-align: middle;
	}
	.mine-view-title{
		display: inline-block;
		max-width: 480px;
		font-size: 18px;
		color: #666666;
		line-height: 30px;
		letter-spacing: 0;
		vertical-align: middle;
	}
	.mine-view-time{
		display: inline-block;
		max-width: 500px;
		font-size: 18px;
		color: #666666;
		line-height: 30px;
		white-space: nowrap;
		margin-left: 20px;
		letter-spacing: 0;
		vertical-align: middle;
	}
	.mine-btn{
		width: 80px;
		height: 24px;
		background: #EEEEEE;
		border-radius: 4px;
		font-size: 14px;
		color: #999999;
		line-height: 24px;
		text-align: center;
		border: none;
		padding: 0;
		display: inline-block;
		letter-spacing: 0;
		float: right;
		margin-top: 18px;
	}
</style>



