<template>
	<div class="page-questions">
		<div class="questions">
			<cus-plate-title :custitle="custitle"></cus-plate-title>
			<div class="page-main">
				<el-row :gutter="26">
				  <el-col :span="6" v-if="navdata.length">
				    <el-menu
				      :default-active="defaultActive"
				      class="el-menu-vertical-demo"
				      @open="handleOpen"
				      @close="handleClose">
              <div v-if="navdata.length" v-for="(item, index) in navdata" :key="index">
                <el-submenu v-if="!!item.son_nav&&item.son_nav.length" class="sub-menu-1" background-color="#0084FF" text-color="#FFFFFF" :index="index+1+''">
                  <template slot="title">
                    <span>{{item.title}}</span>
                  </template>
                  <div v-for="(itemone, indexone) in item.son_nav" :key="indexone">
                    <el-submenu v-if="!!itemone.son_nav&&itemone.son_nav.length" :index="(index+1) + '-' + (indexone+1)">
                      <template slot="title">{{itemone.title}}</template>
                      <div v-for="(itemtwo, indextwo) in itemone.son_nav" :key="indextwo">
                        <el-menu-item :index="(index+1) + '-' + (indexone+1) + '-' + (indextwo+1)" @click="toListPage(index, indexone, indextwo)">{{itemtwo.title}}</el-menu-item>
                      </div>
                    </el-submenu>
                    <el-menu-item v-else :index="(index+1) + '-' + (indexone+1)" @click="toListPage(index, indexone)">{{itemone.title}}</el-menu-item>
                  </div>
                </el-submenu>
                <el-menu-item v-else class="sub-menu-1" :index="index+1+''" @click="toListPage(index)">
                  <span slot="title">{{item.title}}</span>
                </el-menu-item>
              </div>
				    </el-menu>
				  </el-col>
				  <el-col :span="navdata.length?18:24">
				    <div class="menu-content-body" v-if="!!data.count&&data.count">
				    	<div class="menu-news-list" v-if="!!data.article&&data.article.length">
				    		<div class="menu-news-item" v-for="(item, index) in data.article" :key="index" @click="toDetails(item)">
				    			<p class="menu-news-content show-line-1"><i class="mn-list-style-disc"></i>{{item.title}}</p>
				    			<span class="menu-news-time">{{item.create_time_txt}}</span>
				    		</div>
				    	</div>
				    	<div class="menu-news-pager">
				    		<el-pagination
							  background
							  layout="pager, prev, next"
							  :page-size="16"
							  :total="data.count"
                @current-change="handlePage"
							  prev-text="上一页"
							  next-text="下一页">
							</el-pagination>
				    	</div>
				    </div>
            <Nodatawithpic v-else></Nodatawithpic>
				  </el-col>
				</el-row>
			</div>
		</div>
	</div>
</template>

<script>
	import api from '@/api/api'
  import CusPlateTitle from '../components/cusplatetitle'
	export default{
		data() {
			return {
			  custitle: '',
        navigate_id: null,
        curtab: 0,
        navdata: [],
        page: 1,
        data: {},
        indexs: {
          index: null,
          indexone: null,
          indexone: null,
        },
        defaultActive: null,  //el-menu的default-active当前取值
      }
		},
    components: {
      CusPlateTitle
    },
		watch: {
      $route(to,from){
        // console.log('from', from)
        this.page = 1
        this.navigate_id = to.query.id||null
        let _title = to.query.title||null
        if(!!_title){
          this.custitle = decodeURIComponent(_title)
        }else{
          this.custitle = ''
        }

        this.defaultActive = this.curtab + 1 + ''
        return this.start();
      }
		},
    /*beforeRouteUpdate(to,from,next){
      this.navigate_id=to.query.id;
      console.log('navigate_id----', this.navigate_id)
      next();
    },*/
    created() {
      this.navigate_id = this.$route.query.id||null
      let _title = this.$route.query.title||null
      if(!!_title){
        this.custitle = decodeURIComponent(_title)
      }else{
        this.custitle = ''
      }

      this.defaultActive = this.curtab + 1 + ''
      this.start()
    },
		methods: {
      handleOpen() {},
      handleClose() {},
      start() {
        this.getSidebar()
        this.requestArticlelist()
      },
      getSidebar() {
        let _this = this
        let promise = new Promise((resolve, reject)=>{
          api.requestarticlenav({
            pid: this.$route.query.pid
          }).then(response=>{
            return response.data
          }).then(res=>{
            if(res.code === 1){
              _this.navdata = res.data||[]
              for(let i=0;i<res.data.length;i++){
                if(res.data[i].id===_this.navigate_id){
                  _this.curtab = i;
                  _this.defaultActive = _this.curtab + 1 + ''
                  break;
                }
              }

                resolve()
            }
          }).catch(err=>{})
        })
        return promise
      },
      requestArticlelist() {
        let navigate_id = null
        if(this.indexs.indextwo!==undefined&&this.indexs.indextwo!==null){
          navigate_id = this.navdata[this.indexs.index].son_nav[this.indexs.indexone].son_nav[this.indexs.indextwo].id
        }else if(this.indexs.indexone!==undefined&&this.indexs.indexone!==null){
          navigate_id = this.navdata[this.indexs.index].son_nav[this.indexs.indexone].id
        }else if(!!this.$route.query.id){
          navigate_id = this.$route.query.id
        }else if(!!this.$route.query.pid){
          navigate_id = this.$route.query.pid
        }
        api.requestarticlelist({
          navigate_id: navigate_id,
          page: this.page,
          num: 16
        }).then(response=>{
          return response.data
        }).then(res=>{
          if(res.code === 1){
            this.data = res.data||{}
          }
        })
      },
      toListPage(index, indexone, indextwo) {
        this.curtab = index
        this.indexs = {
          index: index,
          indexone: indexone||null,
          indextwo: indextwo||null,
        }
        /*if(this.indexs.indextwo!==undefined&&this.indexs.indextwo!==null){
          this.defaultActive = (index+1)+'-'+(indexone+1)+'-'+(indextwo+1)
        }else if(this.indexs.indexone!==undefined&&this.indexs.indexone!==null){
          this.defaultActive = (index+1)+'-'+(indexone+1)
        }else{
          this.defaultActive = (index+1)+''
        }*/
        this.defaultActive = (index+1)+''

        let navigate_id = null
        if(this.indexs.indextwo!==undefined&&this.indexs.indextwo!==null){
          navigate_id = this.navdata[this.indexs.index].son_nav[this.indexs.indexone].son_nav[this.indexs.indextwo].id
        }else if(this.indexs.indexone!==undefined&&this.indexs.indexone!==null){
          navigate_id = this.navdata[this.indexs.index].son_nav[this.indexs.indexone].id
        }else {
          navigate_id = this.navdata[this.indexs.index].id
        }
        // else if(!!this.$route.query.id){
        //   navigate_id = this.$route.query.id
        // }else if(!!this.$route.query.id){
        //   navigate_id = this.$route.query.pid
        // }
        // console.log('navigate_id---', navigate_id)

        this.$router.push({path: '/questions', query: {
            pid: this.$route.query.pid,
            id: navigate_id,
            title: this.custitle
          }})
      },
      handlePage(currentPage) {
        this.page = currentPage
        this.requestArticlelist()
      },
      toDetails(item) {
        this.$router.push({path: '/newsdetails', query:{id: item.id}})
      }
		},
	}
</script>

<style scoped>
	.page-questions{
		width: 100%;
		background: #FFFFFF;
		padding-bottom: 40px;
	}
	.questions{
		width: 100%;
		min-height: 800px;
	}
	.page-main{
		width: 1200px;
		margin: 10px auto 0;
	}
	.sub-menu-1{
		margin-top: 20px;
		background: #F7F9FA;
		border: 1px solid #EEEEEE;
	}
	.menu-content-body{
		width: 100%;
	}
	.menu-news-pager{
		width: 100%;
		margin-top: 40px;
		text-align: center;
	}
</style>



