<template>
	<div class="page-appointment">
		<div class="appointment">
			<cus-pagetitle ptitle="预约咨询" pdesc="一对一咨询  为考生筛选最最强的院校专业"></cus-pagetitle>
			<div class="cus-radio-nav">
				<div class="cus-radio-nav-inner">
					<el-radio-group v-model="radiobatch">
				      <el-radio-button style="margin-right: 5px;" label="普通考生"></el-radio-button>
				      <el-radio-button label="提前批"></el-radio-button>
				    </el-radio-group>
				</div>
			</div>
			<div v-if="radiobatch==='普通考生'" class="form-appointment">
				<div class="form-row">
					姓名：
					<el-input
						class="input-appointment"
					  	placeholder=""
					    v-model="realname">
					</el-input>
				</div>
				<div class="form-row">
					手机号：
					<el-input
						class="input-appointment"
					  	placeholder=""
					    v-model="mobile">
					</el-input>
				</div>
				<div class="form-row" v-if="subjects.length>0">
					学科性质：
					<el-radio-group class="checkbox-appointment" v-model="checkedSubjects">
					    <el-radio v-for="item in subjects" :label="item.name" :key="item.id">{{item.name}}</el-radio>
					</el-radio-group>
				</div>
				<div class="form-row">
					总分：
					<el-input
						class="input-appointment"
					  	placeholder=""
					    v-model="scores">
					</el-input>
				</div>
				<div class="form-row">
					位次：
					<el-input
						class="input-appointment"
					  	placeholder=""
					    v-model="rank">
					</el-input>
				</div>
				<div class="form-row">
					语文成绩：
					<el-input
						class="input-appointment"
					  	placeholder=""
					    v-model="chinese">
					</el-input>
				</div>
				<div class="form-row">
					数学成绩：
					<el-input
						class="input-appointment"
					  	placeholder=""
					    v-model="math">
					</el-input>
				</div>
				<div class="form-row">
					英语成绩：
					<el-input
						class="input-appointment"
					  	placeholder=""
					    v-model="english">
					</el-input>
				</div>
				<div class="form-row">
					文综成绩：
					<el-input
						class="input-appointment"
					  	placeholder=""
					    v-model="arts">
					</el-input>
				</div>
				<div v-show="false" class="form-row">
					理综成绩：
					<el-input
						class="input-appointment"
					  	placeholder=""
					    v-model="synthesis">
					</el-input>
				</div>
				<div class="form-row">
					地域：
					<el-input
						class="input-appointment"
					  	placeholder=""
					    v-model="region">
					</el-input>
					<span style="font-size: 12px;color: #EE2929;line-height: 20px;font-weight: 500;margin-left: 10px;">*输入地域则会直接筛选该地域符合条件的学校，否则显示全部地区院校</span>
				</div>
				<div class="form-row" v-if="batchs.length>0">
					批次：
					<el-checkbox-group class="checkbox-appointment" v-model="checkedBatchs">
					    <el-checkbox v-for="item in batchs" :label="item.name" :key="item.id">{{item.name}}</el-checkbox>
					</el-checkbox-group>
				</div>
			</div>
			<div v-if="radiobatch==='提前批'" class="form-appointment">
				<div class="form-row">
					总分：
					<el-input
						class="input-appointment"
					  	placeholder=""
					    v-model="scores">
					</el-input>
				</div>
				<div class="form-row">
					位次：
					<el-input
						class="input-appointment"
					  	placeholder=""
					    v-model="rank">
					</el-input>
				</div>
				<div class="form-row" v-if="subjectlist.length>0">
					科目：
					<el-radio-group class="checkbox-appointment" v-model="checkedSubjectlist">
					    <el-radio v-for="item in subjectlist" :label="item.name" :key="item.id">{{item.name}}</el-radio>
					</el-radio-group>
				</div>
				<div class="form-row">
					科目一成绩：
					<el-input
						class="input-appointment"
					  	placeholder=""
					    v-model="subjectscore">
					</el-input>
				</div>
				<div class="form-row">
					身高要求：
					<el-input
						class="input-appointment"
					  	placeholder=""
					    v-model="heightrequire">
					</el-input>
				</div>
				<div class="form-row">
					地域：
					<el-input
						class="input-appointment"
					  	placeholder=""
					    v-model="region">
					</el-input>
					<span style="font-size: 12px;color: #EE2929;line-height: 20px;font-weight: 500;margin-left: 10px;">*输入地域则会直接筛选该地域符合条件的学校，否则显示全部地区院校</span>
				</div>
			</div>
			<div class="appointment-btns">
				<el-button class="appointment-btn btn-submit" type="primary">立即查询</el-button>
			</div>
		</div>
		<cus-messageboxcheck :dialogvisible="dialogCheckVisible"></cus-messageboxcheck>
	</div>
</template>

<script>
	import CusPagetitle from '../components/pagetitle'
	import CusSinglecell from '../components/singlecell'
	import CusMessageboxcheck from '../components/cusmessageboxcheck'
	export default{
		created() {
			//this.msgboxSaveSuccess()
		},
		methods: {
			
		},
		data() {
			return {
				radiobatch: '普通考生',
				realname: '',
				mobile: '',
				scores: '',
				rank: '',
				subjects: [{
					id: 1,
					name: '文科'
				},{
					id: 2,
					name: '理科'
				}],
				checkedSubjects: [],
				chinese: '',
				math: '',
				english: '',
				arts: '',
				synthesis: '',
				batchs: [{
					id: 1,
					name: '本科一批'
				},{
					id: 2,
					name: '本科二批'
				},{
					id: 3,
					name: '专科'
				},{
					id: 4,
					name: '提前批'
				},{
					id: 5,
					name: '直招士官'
				},{
					id: 6,
					name: '地方专项'
				},{
					id: 7,
					name: '艺术类'
				}],
				checkedBatchs: [],
				subjectlist: [{
					id: 1,
					name: '科目一'
				},{
					id: 2,
					name: '科目二'
				},{
					id: 3,
					name: '科目三'
				}],
				checkedSubjectlist: [],
				subjectscore: '',
				heightrequire: '',
				region: '',
				dialogCheckVisible: false
			}
		},
		components: {
			CusPagetitle,
			CusSinglecell,
			CusMessageboxcheck
		}
	}
</script>

<style scoped>
	.page-appointment{
		width: 100%;
	}
	.appointment{
		width: 1200px;
		min-height: 800px;
		margin: 30px auto;
		background: #FFFFFF;
		border: 1px solid #EEEEEE;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.cus-radio-nav{
		width: 100%;
		padding: 0 20px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.cus-radio-nav-inner{
		width: 100%;
		border-bottom: 1px solid #0084FF;
		font-size: 0;
		line-height: 0;
		padding-top: 24px;
	}
	.form-appointment{
		width: 100%;
		padding: 5px 20px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.form-row{
		width: 100%;
		font-size: 18px;
		color: #666666;
		line-height: 20px;
		font-weight: 500;
		margin: 25px auto;
	}
	.input-appointment{
		width: 600px;
		height: 44px;
		font-size: 16px;
		color: #666666;
		line-height: 44px;
		vertical-align: text-top;
		vertical-align: middle;
	}
	.checkbox-appointment{
		display: inline-block;
	}
	hr{
	    height: 1px;
	    background: #BFBFBF;
	}
	.appointment-btns{
		width: 100%;
		padding: 0 20px;
		box-sizing: border-box;
		font-size: 0;
		line-height: 0;
		margin-bottom: 45px;
	}
	.appointment-btn{
		width: 200px;
		height: 44px;
		border-radius: 6px;
		font-size: 18px;
		color: #FFFFFF;
		line-height: 44px;
		text-align: center;
		font-weight: 500;
		box-sizing: border-box;
		padding-top: 0;
		padding-bottom: 0;
	}
	.btn-submit{
		background: #FACD89;
		border-color: #FACD89;
		margin: 30px 0;
	}
	.btn-add{
		background: #F29B76;
		border-color: #FACD89;
	}
	.el-dialog__body{
		padding-top: 0 !important;
	}
</style>


