<template>
    <div class="template-nodata">
      <div class="nodata-container">
        <img class="pic-nodata" src="../../assets/images/pic_nodata.png" alt="">
        <div class="nodata-content">{{content}}</div>
      </div>
    </div>
</template>

<script>
  export default {
    name: 'nodata',
    props: ['cuscontent'],
    data() {
      return {
        content: this.cuscontent || '暂无数据...'
      }
    }
  }
</script>

<style scoped>
.template-nodata{
  width: 100%;
}
.nodata-container{
  width: 100%;
  padding: 24px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.pic-nodata{
  width: 200px;
  height: auto;
  display: block;
  /*margin: 100px auto 20px;*/
  margin: 30px auto 20px;
}
  .nodata-content{
    width: 100%;
    font-size: 14px;
    color: #666666;
    line-height: 20px;
    text-align: center;
  }
</style>



