<template>
	<div class="page-about">
		<div class="about">
			<cus-mine-base curpage="关于我们">
				<div class="mine-content">
					<div class="cus-radio-nav">
						<div class="cus-radio-nav-inner">
							<el-radio-group v-model="navtitle">
						      <el-radio-button style="margin-right: 5px;" label="简介"></el-radio-button>
						      <el-radio-button label="用户协议"></el-radio-button>
						    </el-radio-group>
						</div>
					</div>
					<div class="about-wrapper">
						<div class="about-view" v-if="navtitle==='简介'">
							<!--<div class="logo-cover">
								<img class="img-logo" :src="about.logo"/>
							</div>-->
							<div class="about-content" v-html="about.content"></div>
						</div>
						<div class="about-view" v-if="navtitle==='用户协议'">
							<!--<h3 class="agreement-title">{{agreement.title}}</h3>-->
							<div class="agreement-content" v-html="agreement.content"></div>
						</div>
					</div>
				</div>
			</cus-mine-base>
		</div>
	</div>
</template>

<script>
	import CusMineBase from '../components/cusminebase'
	import api from '@/api/api'
	export default{
		created() {
			this.getsynopsis()
			this.getagreement()
		},
		data() {
			return {
				navtitle: '简介',
				about: {
					content: ''
				},
				agreement: {
					content: ''
				}
			}
		},
		methods: {
			getsynopsis() {
				var _this = this
				api.requestsynopsis().then((res) => {
					return res.data
				}).then((res) => {
					if(res.code === 1){
						_this.about = {
							content: res.data
						}
					}
				}).catch((err) => {
					console.log('err:', err)
				})
			},
			getagreement() {
				var _this = this
				api.requestagreement().then((res) => {
					return res.data
				}).then((res) => {
					if(res.code === 1){
						_this.agreement = {
							content: res.data
						}
					}
				}).catch((err) => {
					console.log('err:', err)
				})
			}
		},
		components: {
			CusMineBase
		}
	}
</script>

<style scoped>
	.page-about{
		width: 100%;
	}
	.about{
		width: 100%;
	}
	.mine-content{
		padding: 18px 24px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.cus-radio-nav{
		width: 100%;
		/*padding: 0 20px;*/
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.cus-radio-nav-inner{
		width: 100%;
		border-bottom: 1px solid #0084FF;
		font-size: 0;
		line-height: 0;
	}
	.about-wrapper{
		width: 100%;
	}
	.about-view{
		width: 100%;
		padding: 25px 0;
	}
	.logo-cover{
		width: 150px;
		height: 150px;
		border-radius: 20px;
		position: relative;
		overflow: hidden;
		margin: 0 auto;
	}
	.img-logo{
		width: auto;
		height: auto;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		z-index: 1;
	}
	.about-content,
	.agreement-content{
		font-size: 16px;
		color: #666666;
		line-height: 30px;
		font-weight: 500;
		/*margin-top: 35px;*/
	}
	.agreement-title{
		font-size: 18px;
		color: #666666;
		line-height: 20px;
		text-align: center;
		font-weight: bold;
	}
</style>



