<template>
	<div class="page-school">
		<div class="school">
      <cus-pagetitle ptitle="院校专业选择" pdesc="专业录取分数线及院校专业查询"></cus-pagetitle>
      <el-row :gutter="20" class="padding-lr-20">
        <el-col :span="17">
          <div class="">
            <div class="news-tabs-card side-l-form-body">
              <h3 class="side-l-form-title">院校专业选择</h3>
              <div class="form-school">
                <div class="form-row" v-if="false">
                  生源地：
                  <el-input
                    class="input-school"
                      placeholder=""
                      v-model="form.students"
                      disabled>
                  </el-input>
                </div>
                <div class="form-row">
                  总分：
                  <el-input
                    class="input-school"
                      placeholder=""
                      v-model="form.score"
                      disabled>
                  </el-input>
                </div>
                <div class="form-row">
                  位次：
                  <el-input
                    class="input-school"
                      placeholder=""
                      v-model="form.ranking"
                      disabled>
                  </el-input>
                </div>
                <div class="form-row" v-if="subjects.length>0">
                  学科性质：
                  <el-radio-group class="checkbox-school" v-model="form.subject">
                      <el-radio v-for="item in subjects" :label="item.subject" :key="item.id" disabled>{{item.name}}</el-radio>
                  </el-radio-group>
                </div>
                <div class="form-row" v-if="batchs.length>0">
                  批次：
                  <el-radio-group class="checkbox-school" v-model="form.batch">
                      <el-radio v-for="item in batchs" :label="item.batch" :key="item.id" disabled>{{item.name}}</el-radio>
                  </el-radio-group>
                </div>
                <div class="form-row" v-if="false">
                  地域：
                  <el-input
                    class="input-school"
                      placeholder=""
                      v-model="form.region">
                  </el-input>
                  <span style="font-size: 12px;color: #EE2929;line-height: 20px;font-weight: 500;margin-left: 10px;">*输入地域则会直接筛选该地域符合条件的学校，否则显示全部地区院校</span>
                </div>
              </div>
              <div class="school-result">
                <h2 class="school-title">估分结果</h2>
                <!--<p class="school-tip">*单击任意学校信息即可查看该校近五年录取信息</p>-->
                <div class="result-body" style="width: 690px;" v-if="listdata.length">
                  <cus-table :custh="custh" :listdata="listdata" @clicktd="handleTd"></cus-table>
                </div>
                <Nodatawithpic v-else cuscontent="暂无结果"></Nodatawithpic>
              </div>
            </div>
          </div>
        </el-col>
        <el-col :span="7">
          <div class="ad-pic-txt">
            <h3 class="news-card-title">平台认证优质学校</h3>
            <div class="ad-pt-list">
              <div class="ad-pt-item">
                <div class="ad-pt-poster"></div>
                <p class="ad-pt-txt show-line-1">学校名简介学校名称简介学校名简介学校学校</p>
                <p class="ad-pt-extra show-line-1">此学校距您位置距离2.5km</p>
              </div>
              <div class="ad-pt-item">
                <div class="ad-pt-poster"></div>
                <p class="ad-pt-txt show-line-1">学校名简介学校名称简介学校名简介学校学校</p>
                <p class="ad-pt-extra show-line-1">此学校距您位置距离2.5km</p>
              </div>
            </div>
          </div>
          <div class="news-txt-card">
            <h3 class="news-card-title">公司动态<span class="news-tabs-more">更多<i class="el-icon-d-arrow-right news-tabs-more-icon"></i></span></h3>
            <div class="news-body">
              <div class="pt-tabs-card">
                <el-row :gutter="13">
                  <el-col :span="8">
                    <div class="pt-tabs-pic-poster"></div>
                  </el-col>
                  <el-col :span="8">
                    <div class="pt-tabs-pic-poster"></div>
                  </el-col>
                  <el-col :span="8">
                    <div class="pt-tabs-pic-poster"></div>
                  </el-col>
                </el-row>
              </div>
              <div class="news-tabs-txt-list">
                <ul class="news-tabs-ul">
                  <li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标</li>
                  <li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题标题标题标题标题标题标题标题</li>
                  <li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题</li>
                  <li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标</li>
                  <li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题题标题标题</li>
                </ul>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
		</div>
	</div>
</template>

<script>
	import CusPagetitle from '../components/pagetitle'
	import CusSinglecell from '../components/singlecell'
	import CusTable from '../components/custable'
  import api from '../api/api'
	export default{
		created() {
			//this.msgboxSaveSuccess()
		},
		methods: {

		},
		data() {
			return {
        form: {
          students: '',
          score: '',
          ranking: '',
          subject: '',
          batch: '',
          region: ''
        },
        subjects: [{
          id: 1,
          name: '文科',
          subject: 'wk'
        },{
          id: 2,
          name: '理科',
          subject: 'lk'
        }],
				checkedSubjects: [],
        batchs: [{
          id: 1,
          name: '本科一批',
          batch: 'yb'
        },{
          id: 2,
          name: '本科二批',
          batch: 'eb'
        },{
          id: 3,
          name: '专科',
          batch: 'gz'
        }
				/*,{
					id: 4,
					name: '提前批'
				},{
					id: 5,
					name: '直招士官'
				},{
					id: 6,
					name: '地方专项'
				},{
					id: 7,
					name: '艺术类'
				}*/
				],
				checkedBatchs: [],
				region: '',
        custh: [{
          id: 1,
          value: '学院名称',
          props: 'college',
          isfilter: false
        },{
          id: 2,
          value: '学院代码',
          props: 'code',
          isfilter: false
        },{
          id: 3,
          value: '专业',
          props: 'major',
          isfilter: true
        },{
          id: 4,
          value: '最高分', //批次
          props: 'batch',
          isfilter: false
        },{
          id: 5,
          value: '最低分', //院校性质
          props: 'nature',
          isfilter: false
        }],
        listdata: [],
				listdatas: [{
          id: 1,
          data: {
            college: '郑州大学',
            code: '00316',
            major: '软件工程',
            batch: '本科一批',
            nature: '211'
          }
        },{
          id: 2,
          data: {
            college: '郑州大学',
            code: '00316',
            major: '软件工程',
            batch: '本科一批',
            nature: '211'
          }
        },{
          id: 3,
          data: {
            college: '郑州大学',
            code: '00316',
            major: '软件工程',
            batch: '本科一批',
            nature: '211'
          }
				}]
			}
		},
    created() {
		  this.requestProfessionalRecordS()
    },
    methods: {
      requestProfessionalRecordS() {
        console.log('this.$route.query.id',this.$route.query.id)
        api.requestprofessionalrecordS({
          id: this.$route.query.id
        }).then(response=>{
          return response.data
        }).then(res=>{
          console.log('res', res)
          if(res.code === 1){
            const {zyrecord} = res.data
            this.form = {
              students: '',
              score: zyrecord.score,
              ranking: zyrecord.ranking,
              subject: zyrecord.subject,
              batch: zyrecord.batch.replace('zy', ''),
              region: ''
            }
            console.log('form', this.form)
            this.listdata = res.data.major.map((obj, index)=>{
              return {
                id: obj.id,
                data: {
                  college: obj.yname,
                  code: obj.code,
                  major: obj.name,
                  batch: obj.maxnum,
                  nature: obj.minnum,
                }
              }
            })
          }
        }).catch(err=>{
          console.log('err', err)
        })
      },
      handleTd() {}
    },
		components: {
			CusPagetitle,
			CusSinglecell,
			CusTable
		}
	}
</script>

<style scoped lang="less">
	.page-school{
		width: 100%;
    /deep/.custable td{
      min-width: 150px;
    }
	}
	.school{
		width: 1200px;
		min-height: 800px;
		margin: 30px auto;
		background: #FFFFFF;
		border: 1px solid #EEEEEE;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.form-school{
		width: 100%;
		padding: 5px 20px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		border-bottom: 1px solid #0084FF;
		margin-bottom: 20px;
	}
	.form-row{
		width: 100%;
		font-size: 18px;
		color: #666666;
		line-height: 20px;
		font-weight: 500;
		margin: 25px auto;
	}
	.input-school{
		width: 600px;
		height: 44px;
		font-size: 16px;
		color: #666666;
		line-height: 44px;
		vertical-align: text-top;
		vertical-align: middle;
	}
	.checkbox-school{
		display: inline-block;
	}
	hr{
	    height: 1px;
	    background: #BFBFBF;
	}
	.school-btns{
		width: 100%;
		padding: 0 20px;
		box-sizing: border-box;
		font-size: 0;
		line-height: 0;
		margin-bottom: 45px;
	}
	.school-btn{
		width: 200px;
		height: 44px;
		border-radius: 6px;
		font-size: 18px;
		color: #FFFFFF;
		line-height: 44px;
		text-align: center;
		font-weight: 500;
		box-sizing: border-box;
		padding-top: 0;
		padding-bottom: 0;
	}
	.btn-submit{
		background: #FACD89;
		border-color: #FACD89;
	}
	.btn-add{
		background: #F29B76;
		border-color: #FACD89;
	}
	.el-dialog__body{
		padding-top: 0 !important;
	}
	.school-title{
		width: 100%;
		padding: 0 20px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		font-size: 26px;
		color: #0084FF;
		line-height: 40px;
		font-weight: bold;
	}
	.school-tip{
		width: 100%;
		font-size: 12px;
		color: #F40303;
		line-height: 20px;
		padding: 0 20px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		margin-top: 20px;
	}
	.result-body{
		padding: 20px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
</style>



