<template>
	<div class="template-login">
		<el-dialog title="欢迎登录云智选" center :visible.sync="dialogFormVisible" :before-close="close">
		  <el-form class="cus-padding-30" :model="form" width="500px">
		    <el-form-item class="form-item" prop="mobile"
		    :rules="[
		      { required: true, message: '手机号不能为空'},
		      { type: 'number', message: '手机号必须为数字'}
		    ]" :class="[{'is-error': errinput==='mobile'}]">
		      <el-input v-model.number="form.mobile" auto-complete="off" placeholder="请输入11位手机号码" clearable maxlength="11" @input="errmsg='';errinput=''"></el-input>
		    </el-form-item>
		    <el-form-item class="form-item" style="margin-bottom: 0;" :class="[{'is-error': errinput==='password'}]">
		      <el-input
			    placeholder="请输入登录密码"
			    v-model="form.password" :type="isPasswordShow?'tel':'password'" @input="errmsg='';errinput=''">
			    <i slot="suffix" class="iconfont" style="padding: 0 5px;box-sizing: border-box;cursor: pointer;font-size: 24px;" @click.stop="switchPasswordShow">{{isPasswordShow?'&#xe60c;':'&#xe6c7;'}}</i>
			  </el-input>
		    </el-form-item>
		  </el-form>
		  <div class="cus-error cus-padding-30" style="width: 100%;font-size: 12px;color: #F56C6C;line-height: 30px;">{{errmsg}}</div>
		  <div class="cus-padding-30" style="width: 100%;text-align: right;">
		  	<a class="btn-forgot" @click="openReset">忘记密码</a>
		  </div>
		  <div class="cus-padding-30" style="width: 100%;text-align: center;margin-top: 50px;">
		    <el-button type="primary" @click="login" style="width: 100%;font-size: 18px;font-weight: bold;letter-spacing: 6px;">登  录</el-button>
		  </div>
		  <div slot="footer" class="dialog-footer cus-padding-30">
		  	<a class="btn-register" @click="openRegister">还没账号？免费注册</a>
		  </div>
		</el-dialog>
	</div>
</template>

<script>
	import api from '@/api/api'
	import Storage from '@/util/storage'
	export default{
		props: ['dialogvisible'],
		watch: {
			dialogvisible(val, oldval) {
				this.dialogFormVisible = val
			}
		},
		data() {
			return {
				dialogFormVisible: this.dialogvisible || false,
				form: {
					mobile: '',
					password: ''
				},
				isPasswordShow: false,
				idCheckAgreement: false,
				errmsg: '',
				errinput: ''
			}
		},
		methods: {
			switchPasswordShow() {
				this.isPasswordShow = !this.isPasswordShow
			},
			checkAgreement() {
				this.idCheckAgreement = !this.idCheckAgreement
			},
			formvalidate() {
				var _this = this
				var promise = new Promise(function(resolve, reject){
					if(_this.form.mobile !== '' && _this.form.mobile !== null){
						if(_this.form.password !== '' && _this.form.password !== null){
							resolve()
						}else{
							_this.errmsg = '请输入密码'
							_this.errinput = 'password'
						}
					}else{
						_this.errmsg = '请输入手机号'
						_this.errinput = 'mobile'
					}
				})

				return promise
			},
			login() {
				var _this = this
				_this.formvalidate().then(() => {
					api.requestlogin({
						mobile: _this.form.mobile,
				        password: _this.form.password
					}).then((res) => {
						return res.data
					}).then((res) => {
						if(res.code === 1){
							Storage.localSet('user_id', res.data.user_id)
							_this.$emit('loginstatus')
							this.close({
								type: '',	//弹窗功能
								nextdialog: false	//是否打开弹窗
							})
						}else{
							this.$message.error(res.msg);
						}
					}).catch((err) => {
						console.log('err:', err)
					})
				}).catch((err) => {
					console.log('err:', err)
				})
			},
			openRegister() {
				this.close({
					type: 'register',	//弹窗功能
					nextdialog: true	//是否打开弹窗
				})
			},
			openReset() {
				this.close({
					type: 'reset',	//弹窗功能
					nextdialog: true	//是否打开弹窗
				})
			},
			close(data) {
				var _this = this
	      if(data!==undefined){
					_this.$emit('closedialog', data);//调用父组件的自定义事件，并传值
				}else{
					_this.$emit('closedialog', {
						type: '',	//弹窗功能
						nextdialog: false	//是否打开弹窗
					});//调用父组件的自定义事件，并传值
				}
			}
		}
	}
</script>

<style scoped>
	/*.el-dialog{
		width: 500px;
	}
	.el-dialog__title {
	    line-height: 24px;
	    font-size: 20px;
	    color: #0084FF;
	    font-weight: bold;
	}
	.el-dialog--center .el-dialog__body {
	    text-align: initial;
	    padding: 50px 50px 0;
	}
	.el-input__inner{
		height: 44px;
		line-height: 44px;
	}
	.el-dialog__headerbtn .el-dialog__close {
	    font-weight: bolder;
	}
	.el-dialog__footer {
	    padding: 10px 50px 50px;
	    text-align: right;
	}*/
	.btn-forgot{
		font-size: 14px;
		color: #0084FF;
		line-height: 40px;
		cursor: pointer;
	}
	.btn-register{
		font-size: 18px;
		color: #0084FF;
		line-height: 40px;
		cursor: pointer;
		font-weight: 500;
	}
</style>



