<template>
	<div class="page-setting">
		<div class="setting">
			<cus-mine-base curpage="设置">
				<div class="mine-content">
					<div class="cus-radio-nav">
						<div class="cus-radio-nav-inner">
							<el-radio-group v-model="navtitle">
						      <el-radio-button style="margin-right: 5px;" label="修改手机号"></el-radio-button>
						      <el-radio-button label="修改密码"></el-radio-button>
						    </el-radio-group>
						</div>
					</div>
					<div class="setting-wrapper">
						<div class="setting-view" v-if="navtitle==='修改手机号'">
						  <el-form class="" :model="form.mobile" width="500px">
						    <!--<el-form-item class="form-item" label="原手机号："
						    :rules="[
						      { required: true, message: '手机号不能为空'},
						      { type: 'number', message: '手机号必须为数字'}
						    ]">-->
						    <!--<el-form-item class="form-item" label="原手机号：" :class="[{'is-error': errinput.mobile==='oldmobile'}]">
						      <el-input v-model.number="form.mobile.oldmobile" auto-complete="off" placeholder="" clearable maxlength="11" @input="errmsg.mobile='';errinput.mobile=''"></el-input>
						    </el-form-item>-->
						    <!--<el-form-item class="form-item" label="原密码：" :class="[{'is-error': errinput.mobile==='password'}]">
						      <el-input
							    placeholder=""
							    v-model="form.mobile.password" type="password" @input="errmsg.mobile='';errinput.mobile=''">
							  </el-input>
						    </el-form-item>-->
						    <el-form-item class="form-item" label="新手机号：" :class="[{'is-error': errinput.mobile==='mobile'}]">
						      <el-input v-model.number="form.mobile.mobile" auto-complete="off" placeholder="" clearable maxlength="11" @input="errmsg.mobile='';errinput.mobile=''"></el-input>
						    </el-form-item>
						    <!--<el-form-item class="form-item" label="图形验证码：" :class="[{'is-error': errinput.mobile==='imgcode'}]">
						      <el-input class="input-code" v-model="form.mobile.imgcode" auto-complete="off" placeholder="" clearable maxlength="6" @input="errmsg.mobile='';errinput.mobile=''"></el-input>
						      <el-button class="get-code" @click="getImgcode('mobile')">
						      	<img class="img-code" :src="srcimgcode.mobile"/>
						      </el-button>
						    </el-form-item>-->
						    <el-form-item class="form-item" label="短信验证码：" :class="[{'is-error': errinput.mobile==='smscode'}]">
						      <el-input class="input-code" v-model="form.mobile.smscode" auto-complete="off" placeholder="" clearable maxlength="6" @input="errmsg.mobile='';errinput.mobile=''"></el-input>
						      <el-button class="get-code" :disabled="countdownlen['mobile']===0?false:true" @click="getSMSCode('mobile')">{{countdownlen['mobile']===0?'获取验证码':countdownlen['mobile']+'秒后重发'}}</el-button>
						    </el-form-item>
						  </el-form>
						  <div class="cus-error" style="width: 100%;font-size: 12px;color: #F56C6C;line-height: 30px;">{{errmsg.mobile}}</div>
						  <div slot="footer" class="dialog-footer">
						    <el-button type="primary" style="width: 300px;font-size: 18px;font-weight: bold;letter-spacing: 6px;" @click="updatesetting('mobile')">确定</el-button>
						  </div>
						</div>
						<div class="setting-view" v-if="navtitle==='修改密码'">
						  <el-form class="" :model="form.pass" width="500px">
                <el-form-item class="form-item" label="手机号：" :class="[{'is-error': errinput.pass==='mobile'}]">
                  <el-input v-model.number="form.pass.mobile" auto-complete="off" placeholder="" clearable maxlength="11" @input="errmsg.pass='';errinput.pass=''"></el-input>
                </el-form-item>
						    <!--<el-form-item class="form-item" label="旧密码：" :class="[{'is-error': errinput.pass==='oldpassword'}]">
						      <el-input
							    placeholder=""
							    v-model="form.pass.oldpassword" type="tel" @input="errmsg.pass='';errinput.pass=''">
							  </el-input>
						    </el-form-item>
						    <el-form-item class="form-item" label="图形验证码：" :class="[{'is-error': errinput.pass==='imgcode'}]">
						      <el-input class="input-code" v-model="form.pass.imgcode" auto-complete="off" placeholder="" clearable maxlength="6" @input="errmsg.pass='';errinput.pass=''"></el-input>
						      <el-button class="get-code" @click="getImgcode('pass')">
						      	<img class="img-code" :src="srcimgcode.pass"/>
						      </el-button>
						    </el-form-item>-->
						    <el-form-item class="form-item" label="短信验证码：" :class="[{'is-error': errinput.pass==='smscode'}]">
						      <el-input class="input-code" v-model="form.pass.smscode" auto-complete="off" placeholder="" clearable maxlength="6" @input="errmsg.pass='';errinput.pass=''"></el-input>
						      <el-button class="get-code" :disabled="countdownlen['pass']===0?false:true" @click="getSMSCode('pass')">{{countdownlen['pass']===0?'获取验证码':countdownlen['pass']+'秒后重发'}}</el-button>
						    </el-form-item>
						    <el-form-item class="form-item" label="新密码：" :class="[{'is-error': errinput.pass==='newpassword'}]">
						      <el-input
							    placeholder=""
							    v-model="form.pass.newpassword" :type="isPasswordShow.pass.newpass?'tel':'password'" @input="errmsg.pass='';errinput.pass=''">
							    <i slot="suffix" class="iconfont" style="padding: 0 5px;box-sizing: border-box;cursor: pointer;font-size: 24px;" @click.stop="isPasswordShow.pass.newpass=!isPasswordShow.pass.newpass">{{isPasswordShow.pass.newpass?'&#xe60c;':'&#xe6c7;'}}</i>
							  </el-input>
						    </el-form-item>
						    <el-form-item class="form-item" label="确认新密码：" :class="[{'is-error': errinput.pass==='password'}]">
						      <el-input
							    placeholder=""
							    v-model="form.pass.password" :type="isPasswordShow.pass.password?'tel':'password'" @input="errmsg.pass='';errinput.pass=''">
							    <i slot="suffix" class="iconfont" style="padding: 0 5px;box-sizing: border-box;cursor: pointer;font-size: 24px;" @click.stop="isPasswordShow.pass.password=!isPasswordShow.pass.password">{{isPasswordShow.pass.password?'&#xe60c;':'&#xe6c7;'}}</i>
							  </el-input>
						    </el-form-item>
						  </el-form>
		  				  <div class="cus-error" style="width: 100%;font-size: 12px;color: #F56C6C;line-height: 30px;">{{errmsg.pass}}</div>
						  <div slot="footer" class="dialog-footer">
						    <el-button type="primary" style="width: 300px;font-size: 18px;font-weight: bold;letter-spacing: 6px;" @click="updatesetting('pass')">确定</el-button>
						  </div>
						</div>
					</div>
				</div>
			</cus-mine-base>
		</div>
		<cus-dialogcommon v-if="dialogSuccessVisible" custitle="提示" :cuscontent="cuscontent + '修改成功!'" :hidecancelbutton="true" cusconfimtitle="去登录" :dialogvisible="dialogSuccessVisible" @handleconfirm="handleConfirm" @handlecancel="handleCancel"></cus-dialogcommon>
		<cus-dialogcommon v-if="dialogErrorVisible" custitle="提示" :cuscontent="cuscontent + '修改失败!'" :hidecancelbutton="true" :hideconfirmbutton="true" :dialogvisible="dialogErrorVisible" @handleconfirm="handleConfirm" @handlecancel="handleCancel"></cus-dialogcommon>
	</div>
</template>

<script>
	import CusMineBase from '../components/cusminebase'
	import CusDialogcommon from '../components/cusdialogcommon'
  import Storage from '@/util/storage'
	import api from '@/api/api'
	import {baseURL} from '@/util/base'
	export default{
		created() {
			//this.msgboxSaveSuccess()
		},
		watch: {
			navtitle: {
				handler:function(val,oldVal){
					this.form = {
						mobile: {
              // password: '',
              mobile: '',
              // imgcode: '',
              smscode: ''
						},
						pass: {
              mobile: '',
              smscode: '',
							newpassword: '',
							password: ''
						},
            countdownlen: {
              mobile: 0,
              pass: 0
            }
					}
	            },
	            // 深度观察
	            deep:true
			}
		},
		data() {
			return {
				navtitle: '修改手机号',
        cuscontent: '手机号',
				form:{
					mobile: {
						// password: '',
						mobile: '',
						// imgcode: '',
						smscode: ''
					},
					pass: {
            mobile: '',
            smscode: '',
            newpassword: '',
            password: ''
					}
				},
				isPasswordShow: {
					pass: {
						newpass: false,
						password: false
					}
				},
				dialogSuccessVisible: false,
				dialogErrorVisible: false,
				// srcimgcode: {
				// 	mobile: this.imgcodeurl(),
				// 	pass: this.imgcodeurl()
				// },
				timer: {
					mobile: null,
					pass: null
				},
				countdownlen: {
					mobile: 0,
					pass: 0
				},
				errmsg: {
					mobile: '',
					pass: ''
				},
				errinput: {
					mobile: '',
					pass: ''
				}
			}
		},
		methods: {
      imgcodeurl() {
        var num=new Date().getTime();
        Storage.localSet('imgcodetime', num)
        return baseURL + '/api/index/imgcode?time=' + num
      },
      getImgcode() {
        this.srcimgcode = this.imgcodeurl()
      },
			countdown(type) {
				var _this = this
				_this.countdownlen[type] = 60
				_this.timer[type] = setInterval(function(){
					if(_this.countdownlen[type]>0){
						_this.countdownlen[type] = _this.countdownlen[type] - 1
					}else{
						clearInterval(_this.timer[type])
					}
				}, 1000)
			},
			codevalidate(type) {
				var _this = this
				var promise = new Promise(function(resolve, reject){
					if(_this.form[type].mobile !== '' && _this.form[type].mobile !== null){
						// if(_this.form[type].imgcode !== '' && _this.form[type].imgcode !== null){
							resolve()
						// }else{
						// 	_this.errmsg[type] = '图形验证码不能为空'
						// 	_this.errinput[type] = 'imgcode'
						// }
					}else{
						_this.errmsg[type] = '手机号不能为空'
						_this.errinput[type] = 'mobile'
					}
				})

				return promise
			},
			getSMSCode(type) {
				var _this = this
        let _range = (type==='mobile'?'changemobile':type==='pass'?'resetpwd':null)

				_this.codevalidate(type).then(() => {
					api.requestgetsmscode({
						mobile: _this.form[type].mobile,
            range: _range,
					}).then((res) => {
						return res.data
					}).then((res) => {
						if(res.code === 1){
							_this.countdown(type)
						}else{
							this.$message.error(res.msg);
						}
					}).catch((err) => {
						console.log('err:', err)
					})
				}).catch((err) => {
					console.log('err:', err)
				})
			},
			formvalidate(type) {
				var _this = this
				var promise = new Promise(function(resolve, reject){
					if(type === 'mobile'){
						_this.formvalidatemobile(type).then(() => {
							resolve()
						}).catch((err) => {
							console.log('err:', err)
						})
					}else if(type === 'pass'){
						_this.formvalidatepass(type).then(() => {
							resolve()
						}).catch((err) => {
							console.log('err:', err)
						})
					}
				})
				return promise
			},
			formvalidatemobile(type) {
				var _this = this
				var promise = new Promise(function(resolve, reject){
					// if(_this.form[type].oldmobile !== '' && _this.form[type].oldmobile !== null){
					// 	if(_this.form[type].password !== '' && _this.form[type].password !== null){
							if(_this.form[type].mobile !== '' && _this.form[type].mobile !== null){
								// if(_this.form[type].imgcode !== '' && _this.form[type].imgcode !== null){
									if(_this.form[type].smscode !== '' && _this.form[type].smscode !== null){
										resolve()
									}else{
										_this.errmsg[type] = '短信验证码不能为空'
										_this.errinput[type] = 'smscode'
									}
								// }else{
								// 	_this.errmsg[type] = '图形验证码不能为空'
								// 	_this.errinput[type] = 'imgcode'
								// }
							}else{
								_this.errmsg[type] = '请输入新手机号'
								_this.errinput[type] = 'mobile'
							}
					// 	}else{
					// 		_this.errmsg[type] = '请输入原密码'
					// 		_this.errinput[type] = 'password'
					// 	}
					// }else{
					// 	_this.errmsg[type] = '请输入原手机号'
					// 	_this.errinput[type] = 'oldmobile'
					// }
				})

				return promise
			},
			formvalidatepass(type) {
				var _this = this
				var promise = new Promise(function(resolve, reject){
					if(_this.form[type].mobile !== '' && _this.form[type].mobile !== null){
            if(_this.form[type].smscode !== '' && _this.form[type].smscode !== null){
              if(_this.form[type].newpassword !== '' && _this.form[type].newpassword !== null){
                if(_this.form[type].password !== '' && _this.form[type].password !== null){
                  if(_this.form[type].newpassword === _this.form[type].password){
                    resolve()
                  }else{
                    _this.errmsg[type] = '两次输入密码不一致'
                  }
                }else{
                  _this.errmsg[type] = '请确认新密码'
                  _this.errinput[type] = 'password'
                }
              }else{
                _this.errmsg[type] = '请设置新密码'
                _this.errinput[type] = 'newpassword'
              }
            }else{
              _this.errmsg[type] = '短信验证码不能为空'
              _this.errinput[type] = 'smscode'
            }
					}else{
            _this.errmsg[type] = '请输入手机号'
            _this.errinput[type] = 'mobile'
					}
				})

				return promise
			},
			updatesetting(type) {
				var _this = this
				_this.formvalidate(type).then(() => {
					if(type === 'mobile'){
						_this.updatemobile(type)
					}else if(type === 'pass'){
						_this.updatepass(type)
					}
				}).catch((err) => {
					console.log('err:', err)
				})
			},
			updatemobile(type) {
				var _this = this
				api.requestsavemobile({
			      	// password: _this.form[type].password,
              mobile: _this.form[type].mobile,
			      	smscode: _this.form[type].smscode
				}).then((res) => {
					return res.data
				}).then((res) => {
          _this.cuscontent = '手机号'
					if(res.code === 1){
						// _this.close({
            // 	type: 'login',	//弹窗功能
            // 	nextdialog: true	//是否打开弹窗
            // })
            Storage.localRemove('user_id')
            _this.dialogSuccessVisible = true
					}else{
						this.$message.error(res.msg);
						_this.dialogErrorVisible = true
          }
				}).catch((err) => {
					console.log('err:', err)
				})
			},
			updatepass(type) {
				var _this = this
				api.requestsavepass({
          mobile: _this.form[type].mobile,
          smscode: _this.form[type].smscode,
          newpass: _this.form[type].newpassword
				}).then((res) => {
					return res.data
				}).then((res) => {
				  _this.cuscontent = '密码'
					if(res.code === 1){
						// _this.close({
						// 	type: 'login',	//弹窗功能
						// 	nextdialog: true	//是否打开弹窗
						// })
            Storage.localRemove('user_id')
            _this.dialogSuccessVisible = true
					}else{
						this.$message.error(res.msg);
            _this.dialogErrorVisible = true
					}
				}).catch((err) => {
					console.log('err:', err)
				})
			},
      close(data) {
        var _this = this
        if(data!==undefined){
          _this.$emit('closedialog', data);//调用父组件的自定义事件，并传值
        }else{
          _this.$emit('closedialog', {
            type: '',	//弹窗功能
            nextdialog: false	//是否打开弹窗
          });//调用父组件的自定义事件，并传值
        }
      },
      handleCancel() {
        this.dialogErrorVisible = this.dialogSuccessVisible = false
      },
      handleConfirm() {
        let _this = this
        this.dialogErrorVisible = this.dialogSuccessVisible = false
        _this.$router.push({path: '/'})
      }
		},
		components: {
			CusMineBase,
			CusDialogcommon
		}
	}
</script>

<style scoped>
	.page-setting{
		width: 100%;
	}
	.setting{
		width: 100%;
	}
	.mine-content{
		padding: 18px 24px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.cus-radio-nav{
		width: 100%;
		/*padding: 0 20px;*/
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.cus-radio-nav-inner{
		width: 100%;
		border-bottom: 1px solid #0084FF;
		font-size: 0;
		line-height: 0;
	}
	.setting-wrapper{
		width: 100%;
	}
	.setting-view{
		width: 100%;
		padding: 25px 0;
	}
</style>



