<template>
	<div class="page-school">
		<div class="school">
			<cus-pagetitle ptitle="选学校" pdesc="一键查询心仪学校历年录取分数线，方便快捷"></cus-pagetitle>
			<el-row :gutter="20" class="padding-lr-20">
				<el-col :span="17">
					<div class="">
						<div class="news-tabs-card side-l-form-body">
							<h3 class="side-l-form-title">选学校</h3>
							<div class="form-school">
								<div class="form-row" v-if="false">
									生源地：
									<el-input
										class="input-school"
									  	placeholder=""
									    v-model="form.students">
									</el-input>
								</div>
								<div class="form-row">
									总分：
									<el-input
										class="input-school"
									  	placeholder=""
									    v-model="form.score">
									</el-input>
								</div>
								<div class="form-row">
									位次：
									<el-input
										class="input-school"
									  	placeholder=""
									    v-model="form.ranking">
									</el-input>
								</div>
								<div class="form-row" v-if="subjects.length>0">
									学科性质：
									<el-radio-group class="checkbox-school" v-model="checkedSubjects">
									    <el-radio v-for="item in subjects" :label="item.subject" :key="item.id">{{item.name}}</el-radio>
									</el-radio-group>
								</div>
								<div class="form-row" v-if="batchs.length>0">
									批次：
									<el-radio-group class="checkbox-school" v-model="checkedBatchs">
									    <el-radio v-for="item in batchs" :label="item.batch" :key="item.id">{{item.name}}</el-radio>
									</el-radio-group>
								</div>
								<div class="form-row" v-if="false">
									地域：
									<el-input
										class="input-school"
									  	placeholder=""
									    v-model="region">
									</el-input>
									<br />
									<span style="font-size: 12px;color: #EE2929;line-height: 20px;font-weight: 500;margin-left: 10px;">*输入地域则会直接筛选该地域符合条件的学校，否则显示全部地区院校</span>
								</div>
							</div>
              <div class="cus-error cus-padding-30" style="width: 100%;font-size: 12px;color: #F56C6C;line-height: 30px;">{{errmsg}}</div>
							<div class="school-btns">
								<el-button class="school-btn btn-submit" type="primary" @click="onSubmit">立即查询</el-button>
							</div>
						</div>
					</div>
				</el-col>
				<el-col :span="7">
					<div class="ad-pic-txt">
						<h3 class="news-card-title">平台认证优质学校</h3>
						<div class="ad-pt-list">
							<div class="ad-pt-item">
								<div class="ad-pt-poster"></div>
								<p class="ad-pt-txt show-line-1">学校名简介学校名称简介学校名简介学校学校</p>
								<p class="ad-pt-extra show-line-1">此学校距您位置距离2.5km</p>
							</div>
							<div class="ad-pt-item">
								<div class="ad-pt-poster"></div>
								<p class="ad-pt-txt show-line-1">学校名简介学校名称简介学校名简介学校学校</p>
								<p class="ad-pt-extra show-line-1">此学校距您位置距离2.5km</p>
							</div>
						</div>
					</div>
					<div class="news-txt-card">
						<h3 class="news-card-title">公司动态<span class="news-tabs-more">更多<i class="el-icon-d-arrow-right news-tabs-more-icon"></i></span></h3>
						<div class="news-body">
							<div class="pt-tabs-card">
								<el-row :gutter="13">
									<el-col :span="8">
										<div class="pt-tabs-pic-poster"></div>
									</el-col>
									<el-col :span="8">
										<div class="pt-tabs-pic-poster"></div>
									</el-col>
									<el-col :span="8">
										<div class="pt-tabs-pic-poster"></div>
									</el-col>
								</el-row>
							</div>
							<div class="news-tabs-txt-list">
								<ul class="news-tabs-ul">
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题标题标题标题标题标题标题标题</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题题标题标题</li>
								</ul>
							</div>
						</div>
					</div>
				</el-col>
			</el-row>
		</div>
		<cus-dialogpay :paymoney="money" :dialogvisible="dialogPayVisible"></cus-dialogpay>
		<cus-paymethod :dialogvisible="dialogPaymethodVisible"></cus-paymethod>
		<cus-messageboxcheck :dialogvisible="dialogCheckVisible"></cus-messageboxcheck>
	</div>
</template>

<script>
	import CusPagetitle from '../components/pagetitle'
	import CusSinglecell from '../components/singlecell'
	import CusDialogpay from '../components/cusdialogpay'
	import CusPaymethod from '../components/cuspaymethod'
	import CusMessageboxcheck from '../components/cusmessageboxcheck'
  import api from '../api/api'
	export default{
		created() {
			//this.msgboxSaveSuccess()
		},
		methods: {

		},
		data() {
			return {
			  form: {
			    students: '',
          score: '',
          ranking: '',
          subject: '',
          batch: '',
          region: ''
        },
				subjects: [{
					id: 1,
					name: '文科',
          subject: 'wk'
				},{
					id: 2,
					name: '理科',
          subject: 'lk'
				}],
				checkedSubjects: [],
				batchs: [{
					id: 1,
					name: '本科一批',
          batch: 'yb'
				},{
					id: 2,
					name: '本科二批',
          batch: 'eb'
				},{
					id: 3,
					name: '专科',
          batch: 'gz'
				}
				/*,{
					id: 4,
					name: '提前批'
				},{
					id: 5,
					name: '直招士官'
				},{
					id: 6,
					name: '地方专项'
				},{
					id: 7,
					name: '艺术类'
				}*/
				],
				checkedBatchs: [],
				dialogPayVisible: false,
				money: '9.00',
				dialogPaymethodVisible: false,
				dialogCheckVisible: false,
        errmsg: ''
			}
		},
    methods: {
      formvalidate() {
        var _this = this
        var promise = new Promise(function(resolve, reject){
          if(_this.form.score !== '' && _this.form.score !== null){
            if(_this.form.ranking !== '' && _this.form.ranking !== null){
              if(_this.checkedSubjects.length > 0){
                if(_this.checkedBatchs.length > 0){
                  resolve()
                }else{
                  _this.errmsg = '请选择批次'
                }
              }else{
                _this.errmsg = '请选择学科性质'
              }
            }else{
              _this.errmsg = '选择位次'
            }
          }else{
            _this.errmsg = '请输入总分'
          }
        })
        return promise
      },
      getformdata() {
        var _this = this
        var promise = new Promise(function(resolve, reject){
          _this.formvalidate().then(()=>{
            let formData = {}
            formData = {
              score: _this.form.score,
              ranking: _this.form.ranking,
              subject: _this.checkedSubjects,
              batch: _this.checkedBatchs+'xx',
            }
            resolve(formData)
          }).catch(err=>{
            console.log('err', err.response)
          })
        })

        return promise
      },
      onSubmit() {
        let _this = this
        _this.getformdata().then(formData=>{
          console.log('form', formData)
          _this.requestCollege(formData)
        }).catch((err) => {
          console.log('err:', err)
        })

      },
      requestCollege(formData) {
		    api.requestcollege(formData).then(response=>{
          return response.data
        }).then(res=>{
          console.log('res', res)
          if(res.code === 1){
            this.$router.push({path: '/schoolresult', query: {id: res.data.mnrecord_id}})
          }
        }).catch(err=>{
          console.log('err', err.response)
        })
      }
    },
		components: {
			CusPagetitle,
			CusSinglecell,
			CusDialogpay,
			CusPaymethod,
			CusMessageboxcheck
		}
	}
</script>

<style scoped>
	.page-school{
		width: 100%;
		background: #FFFFFF;
		padding: 30px 0;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.school{
		width: 1200px;
		min-height: 800px;
		margin: 0 auto;
		background: #FFFFFF;
		border: 1px solid #EEEEEE;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.form-school{
		width: 100%;
		padding: 5px 20px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.form-row{
		width: 100%;
		font-size: 18px;
		color: #666666;
		line-height: 30px;
		font-weight: 500;
		margin: 25px auto;
	}
	.input-school{
		width: 600px;
		height: 44px;
		font-size: 16px;
		color: #666666;
		line-height: 44px;
		vertical-align: text-top;
		vertical-align: middle;
	}
	.checkbox-school{
		display: inline-block;
		max-width: 90%;
		vertical-align: top;
	}
	hr{
	    height: 1px;
	    background: #BFBFBF;
	}
	.school-btns{
		width: 100%;
		padding: 0 20px;
		box-sizing: border-box;
		font-size: 0;
		line-height: 0;
		margin-bottom: 45px;
	}
	.school-btn{
		width: 200px;
		height: 44px;
		border-radius: 6px;
		font-size: 18px;
		color: #FFFFFF;
		line-height: 44px;
		text-align: center;
		font-weight: 500;
		box-sizing: border-box;
		padding-top: 0;
		padding-bottom: 0;
	}
	.btn-submit{
		background: #FACD89;
		border-color: #FACD89;
	}
	.btn-add{
		background: #F29B76;
		border-color: #FACD89;
	}
	.el-dialog__body{
		padding-top: 0 !important;
	}
</style>



