<template>
	<div class="template-footer">
		<!--底部-项目专用-->
		<div class="footer-wrapper">
			<div class="footer">
				<table class="table-footer" border="0" cellspacing="24" cellpadding="0">
					<tr>
						<td class="footer-content">
              <div>
                <div class="footer-title-row"><img v-if="!!siteInfo('logo')" class="footer-logo" :src="(baseURL + siteInfo('logo'))" alt=""><h2 class="footer-title-txt">{{!!siteInfo('name')&&siteInfo('name')||null}}</h2></div>
                <div class="footer-content-txt">
                  {{!!siteInfo('phone')&&('电话：' + siteInfo('phone'))||null}}<br />
                  {{!!siteInfo('fax')&&('传真：' + siteInfo('fax'))||null}}<br />
                  {{!!siteInfo('mail')&&('邮箱：' + siteInfo('mail'))||null}}<br />
                  {{!!siteInfo('address')&&siteInfo('address')||null}}
                </div>
              </div>
						</td>
						<td width="120">
							<img class="img-qrcode" src="../../build/logo.png"/>
							<div class="footer-content-txt">高效快选微信公众号</div>
						</td>
						<td width="120">
							<img class="img-qrcode" src="../../build/logo.png"/>
							<div class="footer-content-txt">高效快选小程序</div>
						</td>
						<td width="120">
							<img class="img-qrcode" src="../../build/logo.png"/>
							<div class="footer-content-txt">高效快选APP下载</div>
						</td>
					</tr>
				</table>
			</div>
			<div class="footer-copyright">
				<div class="footer-about-link">
					<div class="footer-alink-item" @click="innerLinker('estimate', true)">在线估分</div>
          <div class="footer-alink-item" @click="innerLinker('school', true)">选学校</div>
          <div class="footer-alink-item" @click="innerLinker('major', true)">选专业</div>
          <div class="footer-alink-item" @click="innerLinker('appointment',true)">预约咨询</div>
          <div class="footer-alink-item" @click="innerLinker('aboutus')">关于我们</div>
          <div class="footer-alink-item" @click="innerLinker('joinus')">招商加盟</div>
				</div>
				<div class="footer-link-list" v-if="linkdata.length">
					<div class="footer-link-item">友情链接:</div>
					<a class="footer-link-item" v-for="(item, index) in linkdata" :key="index" :href="item.href">{{item.name}}</a>
					<!--<div class="footer-link-item">网站链接</div>
					<div class="footer-link-item">网站链接</div>
					<div class="footer-link-item">网站链接</div>
					<div class="footer-link-item">网站链接</div>
					<div class="footer-link-item">网站链接</div>
					<div class="footer-link-item">网站链接</div>
					<div class="footer-link-item">网站链接</div>
					<div class="footer-link-item">网站链接</div>-->
				</div>
				<div class="copyright">
          {{!!siteInfo('name')&&(siteInfo('name') + '公司版权所有&nbsp;&nbsp;')||null}}河南云互联科技有限公司提供技术支持<br />
          <a href="http://www.miit.gov.cn/">{{!!siteInfo('beian')&&('备案号' + siteInfo('beian'))||null}}</a>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
  import api from '../api/api'
  import Storage from '../util/storage'
  export default {
    data() {
      return {
        linkdata: [],
        sitedata: []
      }
    },
    created() {
      let _this = this
      _this.requestlinklist()
      _this.requestsite()
    },
    methods: {
      siteInfo(name) {
        let cursite = this.sitedata.filter((fobj, findex)=>{
          return name === fobj.name
        })
        if(cursite.length>0){
          return cursite[0].value
        }else{
          return false
        }
      },
      requestlinklist(page, num) {
        let _this = this
        api.requestlinklist({
          location: 'bottom'
        }).then(response=>{
          return response.data
        }).then(res=>{
          if(res.code===1){
            _this.linkdata = res.data || [];
          }
        }).catch(err=>{
          console.log('err', err)
        })
      },
      innerLinker(pathname, needlogin) {
        let _this = this
        if(!!needlogin){
          let user_id = Storage.localGet('user_id')
          if(!!user_id&&user_id!==''&&user_id!==null){
            _this.$router.push({path: '/' + pathname})
          }else{
            _this.$message.info('请登录！！')
          }
        }else{
          _this.$router.push({path: '/' + pathname})
        }
      },
      requestsite() {
        let _this = this
        api.requestsite().then(response=>{
          return response.data
        }).then(res=>{
          if(res.code === 1){
            _this.sitedata = res.data || [];
          }
        }).catch(err=>{
          console.log('err', err)
        })
      },
    }
  }
</script>

<style scoped>
	.footer-wrapper{
		width: 100%;
	}
	.footer{
		width: 100%;
		background: #313131;
	}
	.table-footer{
		width: 1200px;
		margin: auto;
	}
	.table-footer th{
		font-size: 18px;
		color: #FFFFFF;
		line-height: 30px;
		font-weight: bold;
		vertical-align: top;
		text-align: left;
	}
	.table-footer td{
		vertical-align: top;
		text-align: left;
	}
	.table-footer td.footer-content{
		line-height: 30px;
	}
  .footer-title-row{
    font-size: 0;
    line-height: 0;
    letter-spacing: -4px;
  }
  .footer-logo{
    width: 30px;
    height: auto;
    vertical-align: middle;
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px;
  }
	.footer-title-txt{
		font-size: 24px;
		color: #FFFFFF;
		line-height: 40px;
		font-weight: bold;
    letter-spacing: 0;
    display: inline-block;
    vertical-align: middle;
	}
	.footer-content-txt{
		font-size: 16px;
		color: #FFFFFF;
		line-height: 40px;
		font-weight: 500;
	}
	.img-qrcode{
		width: 152px;
		height: 152px;
		display: block;
	}
	.footer-copyright{
		width: 100%;
		background: #1B1B1B;
		padding: 10px 0;
	}
	.footer-about-link,
	.footer-link-list{
		width: 1200px;
		margin: 0 auto;
		font-size: 0;
		line-height: 0;
		letter-spacing: -4px;
		text-align: center;
	}
	.footer-alink-item,
	.footer-link-item{
		font-size: 12px;
		color: #FFFFFF;
		line-height: 24px;
		letter-spacing: 0;
		font-weight: 500;
		display: inline-block;
		vertical-align: middle;
	}
	.footer-alink-item::before,
	.footer-alink-item::after,
	.footer-link-item::before,
	.footer-link-item::after{
		content: '';
		display: inline-block;
		vertical-align: middle;
		width: 10px;
		height: 12px;
	}
	.footer-alink-item:not(:first-child)::before{
		border-left: 1px solid #FFFFFF;
	}
	.copyright{
		width: 1200px;
		margin: 0 auto;
		font-size: 12px;
		color: #FFFFFF;
		line-height: 24px;
		text-align: center;
		font-weight: 500;
	}
</style>



