<template>
	<div class="page-major">
		<div class="major">
			<cus-pagetitle ptitle="院校专业选择" pdesc="专业录取分数线及院校专业查询">
				<el-button type="info" class="cus-btn_info">返回上一页</el-button>
			</cus-pagetitle>
			<div class="form-major">
				<div class="form-row">
					地域：
					<el-input
						class="input-major"
					  	placeholder=""
					    v-model="region">
					</el-input>
					<!--<span style="font-size: 12px;color: #EE2929;line-height: 20px;font-weight: 500;margin-left: 10px;">*输入地域则会直接筛选该地域符合条件的学校，否则显示全部地区院校</span>-->
				</div>
				<div class="form-row">
					总分：
					<el-input
						class="input-major"
					  	placeholder=""
					    v-model="scores">
					</el-input>
				</div>
				<div class="form-row">
					位次：
					<el-input
						class="input-major"
					  	placeholder=""
					    v-model="rank">
					</el-input>
				</div>
				<div class="form-row" v-if="subjects.length>0">
					学科性质：
					<el-radio-group class="checkbox-major" v-model="checkedSubjects">
					    <el-radio v-for="item in subjects" :label="item.name" :key="item.id">{{item.name}}</el-radio>
					</el-radio-group>
				</div>
				<div class="form-row">
					语文成绩：
					<el-input
						class="input-major"
					  	placeholder=""
					    v-model="chinese">
					</el-input>
				</div>
				<div class="form-row">
