					<el-input
						class="input-major"
					  	placeholder=""
					    v-model="math">
					</el-input>
				</div>
				<div class="form-row">
					英语成绩：
					<el-input
						class="input-major"
					  	placeholder=""
					    v-model="english">
					</el-input>
				</div>
				<div class="form-row">
					文综成绩：
					<el-input
						class="input-major"
					  	placeholder=""
					    v-model="arts">
					</el-input>
				</div>
				<div class="form-row">
					理综成绩：
					<el-input
						class="input-major"
					  	placeholder=""
					    v-model="synthesis">
					</el-input>
				</div>
				<div class="form-row">
					位次：
					<el-input
						class="input-major"
					  	placeholder=""
					    v-model="rank">
					</el-input>
				</div>
				<div class="form-row" v-if="batchs.length>0">
					批次：
					<el-checkbox-group class="checkbox-major" v-model="checkedBatchs">
					    <el-checkbox v-for="item in batchs" :label="item.name" :key="item.id">{{item.name}}</el-checkbox>
					</el-checkbox-group>
				</div>
			</div>
			<div class="major-result">
				<h2 class="major-title">估分结果</h2>
				<p class="major-tip">*单击任意学校信息即可查看该校近五年招生信息</p>
				<div class="result-body" style="width: 850px;">
					<cus-table :custh="custh" :listdata="listdata"></cus-table>
				</div>
			</div>
		</div>
	</div>
</template>

