			return {
				scores: '',
				rank: '',
				subjects: [{
					id: 1,
					name: '文科'
				},{
					id: 2,
					name: '理科'
				}],
				checkedSubjects: [],
				chinese: '',
				math: '',
				english: '',
				arts: '',
				synthesis: '',
				batchs: [{
					id: 1,
					name: '本科一批'
				},{
					id: 2,
					name: '本科二批'
				},{
					id: 3,
					name: '专科'
				},{
					id: 4,
					name: '提前批'
				},{
					id: 5,
					name: '直招士官'
				},{
					id: 6,
					name: '地方专项'
				},{
					id: 7,
					name: '艺术类'
				}],
				checkedBatchs: [],
				region: '',
				custh: [{
					id: 1,
					value: '学院名称',
					props: 'college',
					isfilter: false
				},{
					id: 2,
					value: '学员代码',
					props: 'code',
					isfilter: false
				},{
					id: 3,
					value: '专业',
					props: 'major',
					isfilter: true
				},{
					id: 4,
					value: '批次',
					props: 'batch',
					isfilter: false
				},{
					id: 5,
					value: '院校性质',
					props: 'nature',
					isfilter: false
				}],
				listdata: [{
					id: 1,
					data: {
						college: '郑州大学',
						code: '00316',
						major: '软件工程',
						batch: '本科一批',
						nature: '211'
					}
				},{
					id: 2,
					data: {
						college: '郑州大学',
						code: '00316',
						major: '软件工程',
						batch: '本科一批',
						nature: '211'
					}
				},{
					id: 3,
					data: {
						college: '郑州大学',
						code: '00316',
						major: '软件工程',
						batch: '本科一批',
						nature: '211'
					}
				}]
			}
		},
		components: {
			CusPagetitle,
			CusSinglecell,
			CusTable
		}
	}
