<template>
	<div class="template-cusmessagebox">
		<!--内容-自定义组件-->
		<el-dialog class="dialog-cusmessagebox" :title="this.custitle||'提示'" center :visible.sync="isShowmessageboxVisible" :before-close="close">
		  <div style="margin: -20px 0 50px;">
		  	  <slot></slot>
		  </div>
		  <div slot="footer">
		  	  <div class="cusmessagebox-btns">
		  	  	<el-button class="cus-btn" type="info" @click="close">取消</el-button>
		  	  	<el-button class="cus-btn" type="primary" style="margin-left: 40px;" @click="confirmDialog">确定</el-button>
		  	  </div>
		  </div>
		</el-dialog>
	</div>
</template>

<script>
	export default{
		props: ['title', 'messageboxvisible'],
		data() {
			return{
				custitle: this.title || '',
				isShowmessageboxVisible: this.messageboxvisible || false,
			}
		},
		watch: {
			messageboxvisible(val){
				//console.log('val:', val)
				this.isShowmessageboxVisible = val
			}
		},
		methods: {
			confirmDialog() {
				this.$emit('confirmdialog')
			},
			close() {
				this.$emit('closedialog')
			}
		}
	}
</script>

<style scoped>
	.messagebox-cusmessagebox{

	}
	.cusmessagebox-msg{
		font-size: 16px;
		color: #666666;
		line-height: 30px;
		text-align: center;
		font-weight: 500;
	}
	.cusmessagebox-agreement{
		width: 100%;
		font-size: 14px;
		color: #666666;
		line-height: 30px;
		font-weight: 500;
		text-align: left;
		margin-bottom: 20px;
	}
	.cusmessagebox-icon-select{
		font-size: 20px;
		vertical-align: middle;
		margin-right: 5px;
		cursor: pointer;
		height: 30px;
	}
	.cusmessagebox-btns{
		width: 100%;
		text-align: center;
		line-height: 0;
		font-size: 0;
	}
	.cus-btn{
		width: 200px;
		height: 44px;
	}
</style>



