<template>
	<div class="page-estimate">
		<div class="estimate">
			<cus-pagetitle ptitle="在线估分" pdesc="输入孩子两次及两次以上的单科及总分即可预估出第三次大的成绩"></cus-pagetitle>
			<el-row :gutter="20" class="padding-lr-20">
				<el-col :span="17">
					<!--<div class="cus-radio-nav">
						<div class="cus-radio-nav-inner">
							<el-radio-group v-model="pageradiotxt">
						      <el-radio-button style="margin-right: 5px;" label="在线估分"></el-radio-button>
						      <el-radio-button label="模拟选学校"></el-radio-button>
						    </el-radio-group>
						</div>
					</div>-->
					<div class="cus-radio-nav">
						<div class="news-tabs-card side-l-form-body">
							<div class="news-tabs-list">
								<span class="news-tabs-item" :class="[{'active': pageradiotxt==='在线估分'}]" @click.stop="pageradiotxt='在线估分'">在线估分</span>
								<span class="news-tabs-item" :class="[{'active': pageradiotxt==='模拟选学校'}]" @click.stop="pageradiotxt='模拟选学校'">模拟选学校</span>
							</div>
							<div v-if="pageradiotxt==='在线估分'">
								<div class="history-estimate" v-if="historys.length>=2">
									<h4 class="history-title">历史成绩</h4>
									<p class="estimate-tip">*历史成绩最多保留最近三次输入的成绩，新的预估分数会根据两次历史成绩和最新输入的成绩预估，修改历史成绩需支付费用，请务必输入真实成绩信息</p>
									<div class="history-list">
										<div class="history-item" v-for="(item, index) in historys" :key="'history' + item.id" v-if="index <= 2" :style="{borderTop: index>0?'1px solid #BFBFBF':'none'}">
                      <div class="history-item-title">2019期末考试</div>
											<div>
                        <cus-singlecell :celltitle="'总分：'" :cellvalue="item.score">
                          <el-button class="btn-history-update" slot="update" type="text"></el-button>
                        </cus-singlecell>
                        <cus-singlecell v-if="item.result&&item.result.length>0" v-for="(sub, subindex) in item.result" :key="'hisub' + sub.subject_id" :celltitle="sub.subject + '：'" :cellvalue="sub.result">
                          <el-button class="btn-history-update" slot="update" type="text" @click="updateMessagebox(index, subindex)">修改</el-button>
                        </cus-singlecell>
                      </div>
										</div>
									</div>
									<div class="latest-result">
										<h2 class="latest-title">上次预估结果</h2>
										<div class="result-row" v-if="results.length>0" v-for="(item, index) in results" :key="'results' + item.id">
											<cus-singlecell :celltitle="'总分：'" :cellvalue="item.scoreD + '-' + item.scoreG"></cus-singlecell>
											<cus-singlecell v-for="(sub, index) in item.resultD" :celltitle="sub.subject + '：'" :cellvalue="sub.result + '-' + item.resultG[index].result" :key="'prev' + index"></cus-singlecell>
											<!--<cus-singlecell :celltitle="'数学：'" :cellvalue="'98'"></cus-singlecell>-->
										</div>
									</div>
								</div>
								<div class="history-estimate" v-if="historys.length===1">
									<div style="padding: 0 50px;-webkit-box-sizing: border-box;-moz-box-sizing: border-box;box-sizing: border-box;">
										<p class="estimate-tip">*首次估分会收取相应的费用，以后每次估分则完全免费，但修改历史成绩需支付相关费用，请务必填写真实成绩信息</p>
									</div>
									<div class="form-row">
										总分：
										<el-input
											class="input-estimate"
										  	placeholder=""
										    v-model="historys[0].score"
										    disabled>
										</el-input>
										<!--<el-button class="estimate-btn-update" type="text" @click="updateMessagebox(0, -1)">修改</el-button>-->
									</div>
									<div class="form-row" v-if="historys[0].result.length>0">
										科目选择：
										<el-checkbox-group class="checkbox-estimate" v-model="historys[0].result">
										    <el-checkbox v-if="historys[0].result&&historys[0].result.length>0" v-for="(item, index) in historys[0].result" :label="item" :key="'subject' + item.subject_id" disabled>{{item.subject}}</el-checkbox>
										</el-checkbox-group>
									</div>
									<div class="form-row" v-if="historys[0].result&&historys[0].result.length>0" v-for="(item, index) in historys[0].result" :label="item" :key="'subject' + item.subject_id">
										{{item.subject}}成绩：
										<el-input
											class="input-estimate"
										  	placeholder=""
										    v-model="item.result" disabled>
										</el-input>
										<el-button class="estimate-btn-update" type="text" @click="updateMessagebox(0, index)">修改</el-button>
									</div>
									<hr v-if="form.length > 0" />
								</div>
								<div class="form-estimate" v-for="(item, index) in form" :key="item.id">
									<hr v-if="index > 0" />
									<div class="form-row">
										总分：
										<!--<el-input
											class="input-estimate"
										  	placeholder=""
										    v-model="item.score">
										</el-input>-->
                    <el-input
                      class="input-estimate"
                      placeholder=""
                      v-model="item.score"
                      disabled>
                    </el-input>
									</div>
									<div class="form-row" v-if="formSubjects.length>0">
										科目选择：
										<el-checkbox-group class="checkbox-estimate" v-model="item.checkedSubjects">
										    <el-checkbox v-for="(sub, subindex) in formSubjects" :label="sub" :key="sub.id" @change="(checked, event) => checkboxchange(checked, event, index)">{{sub.name}}</el-checkbox>
										</el-checkbox-group>
									</div>
									<div class="form-row" v-if="item.checkedSubjects.length > 0" v-for="sub in item.checkedSubjects" :key="'subscore' + sub.id">
										{{sub.name}}成绩：
										<el-input
											class="input-estimate"
										  	placeholder=""
										    v-model="item.checkedForm[sub.id]">
										</el-input>
                    <el-button type="text" @click="setFullScore(sub.name)">{{!!fullscore[sub.name]&&fullscore[sub.name]!==''&&fullscore[sub.name]!==null?'修改':'设置总分值'}}</el-button>
									</div>
								</div>
								<div class="cus-error cus-padding-30" style="width: 100%;font-size: 12px;color: #F56C6C;line-height: 30px;">{{errmsg}}</div>
								<div class="estimate-btns" v-if="historys.length + form.length < 2">
									<el-button class="estimate-btn btn-save" type="primary" :disabled="showaddbtn" @click="saveScore">保存成绩</el-button>
									<el-button class="estimate-btn btn-add" type="primary" @click="addScoreItem" v-if="showaddbtn">增加成绩</el-button>
								</div>
								<div class="estimate-btns" v-else>
									<el-button class="estimate-btn btn-save" type="primary" @click="handleCompute">立即预估</el-button>
								</div>
							</div>
							<div v-if="pageradiotxt==='模拟选学校'">
								<div style="padding: 20px 20px 0;-webkit-box-sizing: border-box;-moz-box-sizing: border-box;box-sizing: border-box;">
									<div class="estimate-tip">*此功能目前只针对高三年级的学生   输入近期模拟考试的总分可预测出能上的学校</div>
								</div>
								<div class="form-estimate">
									<div class="form-row">
										总分：
										<el-input
											class="input-estimate"
										  	placeholder=""
										    v-model="school.score">
										</el-input>
									</div>
								</div>
								<div class="cus-error cus-padding-30" style="width: 100%;font-size: 12px;color: #F56C6C;line-height: 30px;">{{errmsgschool}}</div>
								<div class="estimate-btns">
									<el-button class="estimate-btn btn-save" type="primary" @click="simulate">立即查询</el-button>
								</div>
							</div>
						</div>
					</div>
				</el-col>
				<el-col :span="7">
					<div class="ad-pic-txt" v-if="advert.length">
						<h3 class="news-card-title">推荐</h3>
						<div class="ad-pt-list">
							<div class="ad-pt-item" v-for="(item, index) in advert" :key="index">
								<div class="ad-pt-poster" :style="{backgroundImage: 'url(' + baseURL + item.image + ')'}"></div>
								<p class="ad-pt-txt show-line-1">{{item.name}}</p>
								<p class="ad-pt-extra show-line-1">{{item.address}}</p>
							</div>
						</div>
					</div>
					<div class="news-txt-card" v-if="hotdata1.length">
						<h3 class="news-card-title">动态资讯<span class="news-tabs-more">更多<i class="el-icon-d-arrow-right news-tabs-more-icon"></i></span></h3>
						<div class="news-body">
							<div class="pt-tabs-card">
								<el-row :gutter="13">
									<el-col :span="8" v-for="(item, index) in hotdata1.slice(0, 3)" :key="index">
										<div class="pt-tabs-pic-poster" :style="{backgroundImage: (!!item.image&&'url(' + baseURL + item.image + ')'||'')}" @click="toDetails(item)"></div>
									</el-col>
								</el-row>
							</div>
							<div class="news-tabs-txt-list" v-if="hotdata1.length>3">
								<ul class="news-tabs-ul">
									<li class="news-tabs-li pt-tabs-li show-line-1" v-for="(item, index) in hotdata1.slice(3)" :key="index" @click="toDetails(item)">{{item.title}}</li>
								</ul>
							</div>
						</div>
					</div>
				</el-col>
			</el-row>
		</div>
		<cus-dialogpay :paymoney="money" :dialogvisible="dialogPayVisible" @closedialog="closedialog" @confirmdialog="confirmdialog"></cus-dialogpay>
		<cus-paymethod :dialogvisible="dialogPaymethodVisible" @closedialog="closemethosdialog" @handlepay="handlepay"></cus-paymethod>
	</div>
</template>

<script>
	import api from '@/api/api'
	import CusPagetitle from '../components/pagetitle'
	import CusSinglecell from '../components/singlecell'
	import CusDialogpay from '../components/cusdialogpay'
	import CusPaymethod from '../components/cuspaymethod'
	export default{
		created() {
			//this.msgboxSaveSuccess()
			this.getSubjects()
			// this.getrequestforecast()
      this.start()
      this.requesthotrrticle(1, 8)
		},
		data() {
			return {
        hotdata1: [],
				pageradiotxt: '在线估分',
				historys: [],
				results: [],
				form: [{
					score: '',
					result: [],
					checkedSubjects: [],
					checkedForm: {},	//选中的科目成绩
					checkedFormArr: []	//选中的科目成绩
				}],
        fullscore: {},
        formSubjects: [],
				// formSubjects: [
			  //       {
			  //           "id": 1,
			  //           "name": "语文"
			  //       },
			  //       {
			  //           "id": 2,
			  //           "name": "数学"
			  //       },
			  //       {
			  //           "id": 3,
			  //           "name": "英语"
			  //       },
			  //       {
			  //           "id": 4,
			  //           "name": "生物"
			  //       },
			  //       {
			  //           "id": 5,
			  //           "name": "地理"
			  //       },
			  //       {
			  //           "id": 6,
			  //           "name": "历史"
			  //       },
			  //       {
			  //           "id": 7,
			  //           "name": "政治"
			  //       }
			  //   ],
				school: {
					score: ''
				},
				showaddbtn: false,
				dialogPayVisible: false,
				money: '9.00',
				dialogPaymethodVisible: false,
				errmsg: '',
				errmsgschool: '',
        advert: [],
        formData: [],
        usage: null, //支付用途
        formupdateData: null
			}
		},
		watch: {
			form: {
				handler(val, oldval){
					this.errmsg = ''
          this.getScore()
				},
				deep: true
			},
			school: {
				handler(val, oldval){
					this.errmsgschool = ''
				},
				deep: true
			}
		},
		methods: {
		  start() {
		    this.getGradesLogList()
      },
      requesthotrrticle(page, num) {
        let _this = this
        api.requesthotrrticle({
          page: page,
          num: num
        }).then(response=>{
          return response.data
        }).then(res=>{
          if(res.code === 1){
            _this.hotdata1 = res.data || [];
          }
        }).catch(err=>{
          console.log('err', err)
        })
      },
		  getScore() {
		    let _this = this
		    for(let i=0; i<_this.form.length; i++){
          let _score = 0
          let addedscore_arr = _this.form[i]
		      for(let key in _this.form[i].checkedForm){
            if(_this.form[i].checkedForm[key]!==''&&_this.form[i].checkedForm[key]!==null){
              _score += (_this.form[i].checkedForm[key] - 0)
              if(_this.form[i].checkedSubjects.length - 1 === Object.keys(_this.form[i].checkedForm).indexOf(key)){
                _this.form[i].score = _score
              }
            }else{
              _this.form[i].score = ''
              return
            }
          }
        }
      },
			msgboxSaveSuccess() {
				this.$alert('成绩已保存，待输入第二次成绩后即可开始估分', '保存成功', {
		          center: true,
		          showConfirmButton: false
		        });
        // this.getrequestforecast()
        this.start()
			},
			selectAgreement() {
				this.isSelectedAggreement = !this.isSelectedAggreement
			},
			getSubjects() {
				var _this = this
				api.requestsubject({
          grade: 1
        }).then((res) => {
					return res.data
				}).then((res) => {
					if(res.code === 1){
            _this.formSubjects = res.data || []
					}else if(res.code === 401){
						this.$message.error(res.msg);
						//_this.form.result = []
						//_this.historys.result = []
					}
				}).catch((err) => {
					console.log('err:', err)
				})
			},
			closedialog() {
				var _this = this
				_this.dialogPayVisible = false
			},
      confirmdialog() {
        var _this = this
        _this.dialogPayVisible = false
        _this.dialogPaymethodVisible = true
      },
      closemethosdialog() {
			  let _this = this
        _this.dialogPaymethodVisible = false
      },
      handlepay(obj) {
        let _this = this
        // console.log('obj.way----', obj.way)
        _this.closemethosdialog()
        switch (_this.usage) {
          case 'compute':
            _this.compute(_this.formData)
            break;
          case 'updateoldresult':
            _this.requestupdateOldResult(_this.formupdateData)
            break;
          default:
            break;
        }
      },
			updateMessagebox(index, subindex) {
				var _this = this
        _this.usage = 'updateoldresult'
				var subject = _this.historys[index].result[subindex] && _this.historys[index].result[subindex].subject || '总分'
				var result = _this.historys[index].result[subindex] && _this.historys[index].result[subindex].result || _this.historys[index].score || 0
				_this.$prompt(subject + ':' + result, '修改历史成绩', {
		          confirmButtonText: '确定',
		          showInput: true,
		          confirmButtonClass: 'cusconfirmclass',
		          showCancelButton: false,
		          inputPlaceholder: '请输入新的' + subject + '成绩',
		          inputType: 'tel',
		          center: true
		        }).then(({ value }) => {
		          if(!!value&&value!==''&&value!==null){
                _this.formupdateData = {
                  id: _this.historys[index].id,
                  subject_id: _this.historys[index].result[subindex].subject_id,
                  result: value
                }
                _this.dialogPayVisible = true
              }else{
                _this.$message({
                  type: 'info',
                  message: '成绩不能为空!'
                });
              }
		        }).catch(() => {
		          _this.$message({
		            type: 'info',
		            message: '取消修改'
		          });
		        });
			},
      requestupdateOldResult(data) {
			  let _this = this
        api.requestupdateOldResult({
          id: data.id,
          subject_id: data.subject_id,
          result: data.result
        }).then(response=>{
          return response.data
        }).then(res=>{
          if(res.code === 1){
            // if(subindex >= 0){
              // _this.historys[index].result[subindex].result = value
              // _this.getrequestforecast()
            this.start()
            // }else if(subindex === -1){
            //   _this.historys[index].score = value
            // }
            _this.$message({
              type: 'success',
              message: '成绩修改成功'
            });
          }
        }).catch(err=>{
          console.log('err', err)
        })
      },
      setFullScore(subject) {
			  let _this = this
        _this.$prompt(!!_this.fullscore[subject]&&_this.fullscore[subject]!==''&&_this.fullscore[subject]!==null?subject + ':' + _this.fullscore[subject]:null, '设置总分值', {
          confirmButtonText: '确定',
          showInput: true,
          confirmButtonClass: 'cusconfirmclass',
          showCancelButton: false,
          inputPlaceholder: '请输入' + subject + '成绩的总分值',
          inputType: 'tel',
          center: true
        }).then(({ value }) => {
          if(value!==''&&value!==null){
            _this.requestSetsubjectFull(subject, value)
          }else{
            _this.$message({
              type: 'info',
              message: '总分值不能为空!'
            });
          }
          // if(subindex >= 0){
          //   _this.historys[index].result[subindex].result = value
          // }else if(subindex === -1){
          //   _this.historys[index].score = value
          // }
          // _this.$message({
          //   type: 'success',
          //   message: '成绩修改成功'
          // });
        }).catch(() => {
          // _this.$message({
          //   type: 'info',
          //   message: '设置失败'
          // });
        });
      },
      requestSetsubjectFull(subject, value) {
			  let _this = this
			  api.requestsetsubjectfull({
          subject_id: subject,
          full: value,
        }).then((res) => {
          return res.data
        }).then((res) => {
          // console.log('res', res)
          if(res.code === 1){
            _this.$set(this.fullscore, subject, value)
            // _this.fullscore[subject] = value
          }else{
            _this.$message({
              type: 'error',
              message: res.msg
            });
          }
        }).catch((err) => {
          console.log('err:', err)
        })
      },
      getGradesLogList() {
        var _this = this
        api.getGradesLogList({
          page: 1
        }).then((res) => {
          return res.data
        }).then((res) => {
          if(res.code === 1){
            _this.historys = res.data || []
          }else{
            _this.$message({
              type: 'error',
              message: res.msg
            });
          }
        }).catch((err) => {
          console.log('err:', err)
        })
      },
			getrequestforecast() {
				var _this = this
				api.requestforecast().then((res) => {
					return res.data
				}).then((res) => {
					if(res.code === 1){
						_this.historys = res.data.resultsData || []
            _this.results = res.data.forecastData || []
            _this.advert = res.data.advertData || []
					}else{
						_this.$message({
				            type: 'error',
				            message: res.msg
				       });
					}
				}).catch((err) => {
					console.log('err:', err)
				})
			},
			addScoreItem() {
				var _this = this
				var options = {
					score: '',
					result: [],
					checkedSubjects: [],
					checkedForm: {},	//选中的科目成绩
					checkedFormArr: []	//选中的科目成绩
				}
				_this.form = [..._this.form, options]
			},
			checkboxchange(checked, event, index){
				var _this = this
				if(!checked){
					_this.$delete(_this.form[index].checkedForm,event.target._value.id)
          let _subject_name_arr = _this.formSubjects.filter((fobj, findex)=>{
            return fobj.id === event.target._value.id
          })
          if(_subject_name_arr.length){
            let _subject_name = _subject_name_arr[0].name
            delete _this.fullscore[_subject_name]
          }
				}else {
          _this.form[index].score = ''
          let _subject_name_arr = _this.formSubjects.filter((fobj, findex)=>{
            return fobj.id === event.target._value.id
          })
          if(_subject_name_arr.length&&_subject_name_arr[0].full&&_subject_name_arr[0].full!==''&&_subject_name_arr[0].full!==null){
            let _subject_name = _subject_name_arr[0].name
            _this.fullscore[_subject_name] = _subject_name_arr[0].full
          }
        }
			},
			objectToArray(i) {
				var _this = this
				var promise = new Promise(function(resolve, reject){
					var arr = []
					_this.form[i].checkedFormArr = []
					var subobj = _this.form[i].checkedForm
					for(var key in subobj){
						arr.push(key + ':' + subobj[key])
						if(arr.length === _this.form[i].checkedSubjects.length){
							_this.form[i].checkedFormArr = arr
							if(i === _this.form.length - 1){
								resolve()
							}
						}
					}
				})

				return promise
			},
			formvalidate() {
				var _this = this
				var promise = new Promise(function(resolve, reject){
					for(var i=0; i<_this.form.length; i++){
						// if(_this.form[i].score !== '' && _this.form[i].score !== null){
							if(_this.form[i].checkedSubjects.length > 0){
								if(Object.keys(_this.form[i].checkedForm).length > 0){
                  if(Object.keys(_this.fullscore).length === Object.keys(_this.form[i].checkedForm).length){
                    _this.objectToArray(i).then(() => {
                      if(i === _this.form.length){
                        resolve()
                      }
                    }).catch(err => {
                      console.log('err:', err)
                    })
                  }else{
                    _this.errmsg = '请为所选科目设置总分值'
                    break;
                  }
								}else{
									_this.errmsg = '单科成绩不能为空'
									break;
								}
							}else{
								_this.errmsg = '选择科目'
								break;
							}
						// }else{
						// 	_this.errmsg = '请输入总分'
						// 	break;
						// }
					}
				})

				return promise
			},
			getformdata() {
				var _this = this
				var promise = new Promise(function(resolve, reject){
					_this.formvalidate().then(() => {
						var formData = []
						for(var i=0; i<_this.form.length; i++){
							formData.push({
								score: _this.form[i].score,
								result: _this.form[i].checkedFormArr.join(',')
							})
							if(i === _this.form.length - 1){
							  _this.formData = formData
								resolve(formData)
							}
						}
					}).catch((err) => {
						console.log('err:', err)
					})
				})

				return promise
			},
			saveScore() {
				var _this = this
				_this.getformdata().then((formData) => {
					api.requestaddresults(formData).then((res) => {
						return res.data
					}).then((res) => {
						if(res.code === 1){
							_this.saveSuccess()
						}else{
							this.$message.error(res.msg);
						}
					}).catch(err => {
						console.log('err:', err)
					})
				}).catch((err) => {
					console.log('err:', err)
				})
			},
			saveSuccess() {
				var _this = this
				_this.showaddbtn = true
				_this.form= []
				// _this.getrequestforecast()
        this.start()
			},
      handleCompute() {
        var _this = this
        _this.usage = 'compute'
        _this.getformdata().then((formData) => {
          if(_this.historys.length===1){
            _this.dialogPayVisible = true
          }else{
            _this.compute(formData)
          }
        }).catch((err) => {
          console.log('err:', err)
        })
      },
			compute(formData) {
				var _this = this
				// _this.getformdata().then((formData) => {
					api.requestcompute(formData).then((res) => {
						return res.data
					}).then((res) => {
						if(res.code === 1){
						  // _this.dialogPayVisible = true
							_this.$router.push({name: 'Estiresult', query: {id: res.data.id, hasRecommend: true, type: 'estimate'}});
						}else{
							this.$message.error(res.msg);
						}
					}).catch(err => {
						console.log('err:', err)
					})
				// }).catch((err) => {
				// 	console.log('err:', err)
				// })
			},
			formsimulatevalidate() {
				var _this = this
				var promise = new Promise(function(resolve, reject){
					if(_this.school.score !== '' && _this.school.score !== null){
						resolve()
					}else{
						_this.errmsgschool = '请输入总分'
					}
				})

				return promise
			},
			simulate() {
				var _this = this
				_this.formsimulatevalidate().then(() => {
					api.requestsimulate({
						score: _this.school.score
					}).then((res) => {
						return res.data
					}).then((res) => {
						if(res.code === 1){
							_this.$router.push({name: 'Estiresult', query: {id: res.data.id, hasRecommend: true, type: 'school'}});
						}else{
							this.$message.error(res.msg);
						}
					}).catch(err => {
						console.log('err:', err)
					})
				}).catch((err) => {
					console.log('err:', err)
				})
			},
      toDetails(item) {
		    this.$router.push({path: '/newsdetails', query: {id: item.id}})
      }
		},
		components: {
			CusPagetitle,
			CusSinglecell,
			CusDialogpay,
			CusPaymethod
		}
	}
</script>

