<template>
	<div class="template-custable">
		<!--表格-->
		<div class="custable">
			<table border="1" cellspacing="0" cellpadding="0" style="width: 100%;">
				<tr v-if="cusths.length>0">
					<th v-for="(item, index) in cusths" :key="'th' + index">
            <!--v-if="!!item.isfilter" v-model="isShow"-->
				        <el-popover
                  v-if="false"
                  value="false"
						  placement="bottom"
						  width="120"
						  trigger="click">
						  <ul style="font-size: 16px;color: #666666;line-height: 30px;">
						  	<!--<li>不限地域</li>-->
						  	<li @click="isShow=false">河南</li>
						  	<!--<li>湖北</li>
						  	<li>湖南</li>
						  	<li>浙江</li>-->
						  </ul>
						  <div slot="reference">
							<span v-html="item.value"></span><i v-if="!!item.isfilter" class="el-icon-caret-bottom" style="font-size: 20px;color: #777777;margin-left: 20px;"></i>
						  </div>
						</el-popover>
						<div v-else slot="reference" v-html="item.value"></div>
					</th>
				</tr>
				<tr v-for="(item, index) in listdatas" :key="'tr' + index" v-if="listdatas.length>0">
					<td v-for="(pval, pkey) in item.data" :key="pkey" :style="{background: index%2===0?'#FFFFFF':'#F5F5F5'}" @click="toDetails(item)">{{pval===''||pval===null?'-':pval}}</td>
				</tr>
			</table>
		</div>
	</div>
</template>

<script>
	export default{
		props: ['custh', 'listdata'],
		data() {
			return{
				cusths: this.custh || [],
				listdatas: this.listdata || [],
        isShow: false
			}
		},
		watch:{
			custh: {
				handler:function(val,oldVal){
	              this.cusths = val
	            },
	            // 深度观察
	            deep:true
			},
			listdata: {
				handler:function(val,oldVal){
	              this.listdatas = val
	            },
	            // 深度观察
	            deep:true
			}
		},
    methods: {
      toDetails(item) {
        this.$emit('clicktd', item)
        // this.$router.push({path: '/schooldetails'})
      }
    }
	}
</script>

<style scoped>
	.custable th {
		min-width: 50px;
	    height: 50px;
	    font-size: 16px;
	    color: #FFFFFF;
	    line-height: 20px;
	    font-weight: bold;
	    background: #BFBFBF;
		padding: 0 5px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		cursor: pointer;
	}
	.custable td{
		min-width: 50px;
		max-height: 40px;
		font-size: 14px;
		color: #666666;
	    line-height: 40px;
		text-align: center;
		border: 1px solid #EEEEEE;
		padding: 0 5px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		cursor: pointer;
	}
</style>



