<template>
	<div class="page-appointment">
		<div class="appointment">
			<cus-mine-base curpage="预约咨询记录">
				<div class="mine-content">
					<ul class="mine-view-ul" v-if="data.length>0">
						<li class="mine-view-li" v-for="(item, index) in data" :key="item.id" :style="{'margin-top': index===0?'0':'26px'}">
							<div class="user-base">
								<span class="txt-user spacing">姓名：{{item.user.realname}}</span>
								<span class="txt-user spacing">预约时间：{{item.time}}</span>
								<span class="txt-user spacing">电话：{{item.user.mobile}}</span>
							</div>
							<div class="record-content">
								<span class="txt-record spacing">学科：{{item.data.subject==='wk'?'文科':'理科'}}</span>
                <span class="txt-record spacing">总分：{{item.data.score}}</span>
                <span class="txt-record spacing">语文：{{item.data.yw}}</span>
								<span class="txt-record spacing">数学：{{item.data.sx}}</span>
								<span class="txt-record spacing">英语：{{item.data.yy}}</span>
								<span class="txt-record spacing">{{item.data.subject==='wk'?'文综':'理综'}}：{{item.data.zh}}</span>
								<span class="txt-record spacing">批次：
									<span v-if="item.data.batch.length&&item.data.batch.split(',').length" v-for="(sub, indsub) in item.data.batch.split(',')" :key="indsub" class="sub-txt spacing">{{sub==='yb'?'一本':sub==='eb'?'二本':'高职高专'}}</span>
								</span>
							</div>
							<div class="address-row">
								<div class="record-base">
									<span class="txt-base spacing">地址：{{item.address}}</span>
									<span class="txt-base spacing">联系电话：{{item.mobile}}</span>
								</div>
								<el-button v-if="false" class="btn-record" type="primary">{{item.status===0?'取消预约':'删除预约记录'}}</el-button>
							</div>
							<div v-if="false" class="appointment-status">{{item.status===0?'未咨询':'已咨询'}}</div>
						</li>
					</ul>
					<Nodatawithpic v-else cuscontent="暂无记录"></Nodatawithpic>
				</div>
			</cus-mine-base>
		</div>
		<cus-dialogcommon v-if="false" custitle="取消提示" cuscontent="您确定要取消本次预约吗？" custips="确认取消预约你所支付的费用将会原路退回" :dialogvisible="dialogTipVisible"></cus-dialogcommon>
		<cus-dialogcommon v-if="false" custitle="删除提示" cuscontent="您确定要删除此预约记录吗？" :dialogvisible="dialogDeleteVisible"></cus-dialogcommon>
	</div>
</template>

<script>
	import CusMineBase from '../components/cusminebase'
	import CusDialogcommon from '../components/cusdialogcommon'
  import api from '../api/api'
	export default{
		data() {
			return {
			  data: [],
				datas: [{
					id: 1,
					user: {
						realname: '在线估分查询',
						mobile: '16638293849'
					},
					time: '2019-08-12 11：52',
					data: {
						subject: '文科',
						score: '660',
						rank: '2',
						chinese: '120',
						math: '140',
						english: '120',
						arts: '240',
						batchs: ['一本', '二本', '艺术类']
					},
					address: '郑州市北三环经三名筑',
					mobile: '16638293849',
					status: 0
				},{
					id: 2,
					user: {
						realname: '在线估分查询',
						mobile: '16638293849'
					},
					time: '2019-08-12 11：52',
					data: {
						subject: '文科',
						score: '660',
						rank: '2',
						chinese: '120',
						math: '140',
						english: '120',
						arts: '240',
						batchs: ['一本', '二本', '艺术类']
					},
					address: '郑州市北三环经三名筑',
					mobile: '16638293849',
					status: 0
				},{
					id: 3,
					user: {
						realname: '在线估分查询',
						mobile: '16638293849'
					},
					time: '2019-08-12 11：52',
					data: {
						subject: '文科',
						score: '660',
						rank: '2',
						chinese: '120',
						math: '140',
						english: '120',
						arts: '240',
						batchs: ['一本', '二本', '艺术类']
					},
					address: '郑州市北三环经三名筑',
					mobile: '16638293849',
					status: 0
				},{
					id: 4,
					user: {
						realname: '在线估分查询',
						mobile: '16638293849'
					},
					time: '2019-08-12 11：52',
					data: {
						subject: '文科',
						score: '660',
						rank: '2',
						chinese: '120',
						math: '140',
						english: '120',
						arts: '240',
						batchs: ['一本', '二本', '艺术类']
					},
					address: '郑州市北三环经三名筑',
					mobile: '16638293849',
					status: 0
				},{
					id: 5,
					user: {
						realname: '在线估分查询',
						mobile: '16638293849'
					},
					time: '2019-08-12 11：52',
					data: {
						subject: '文科',
						score: '660',
						rank: '2',
						chinese: '120',
						math: '140',
						english: '120',
						arts: '240',
						batchs: ['一本', '二本', '艺术类']
					},
					address: '郑州市北三环经三名筑',
					mobile: '16638293849',
					status: 1
				}],
				dialogTipVisible: false,
				dialogDeleteVisible: true
			}
		},
    created() {
      this.requestAdvisoryRecord()
    },
    methods: {
      requestAdvisoryRecord() {
        api.requestadvisoryrecord().then(response=>{
          return response.data
        }).then(res=>{
          if(res.code === 1){
            this.data = res.data.map((obj, index)=>{
              return {
                id: obj.id,
                user: {
                  realname: obj.realname,
                  mobile: obj.mobile
                },
                time: obj.time,
                data: {
                  subject: obj.subject,
                  score: obj.score,
                  ranking: obj.ranking,
                  yw: obj.yw,
                  sx: obj.sx,
                  yy: obj.yy,
                  zh: obj.zh,
                  batch: obj.batch
                },
                address: '郑州市北三环经三名筑',
                mobile: '16638293849',
                status: 0
              }
            })
          }
        }).catch(err=>{
          console.log('err', err)
        })
      },

      toDetails(item) {
        this.$router.push({path: '/majorresult', query: {id: item.id}})
      }
    },
		components: {
			CusMineBase,
			CusDialogcommon
		}
	}
</script>

<style scoped>
	.page-appointment{
		width: 100%;
	}
	.appointment{
		width: 100%;
	}
	.mine-content{
		padding: 18px 24px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.mine-view-ul{
		width: 100%;
	}
	.mine-view-li{
		width: 100%;
		min-height: 60px;
		background: #F6FAFE;
		border-radius: 6px;
		position: relative;
		padding: 20px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.user-base{
		width: 100%;
		font-size: 0;
		line-height: 0;
		letter-spacing: -4px;
	}
	.txt-user{
		font-size: 18px;
		color: #666666;
		line-height: 30px;
		font-weight: 500;
		letter-spacing: 0;
	}
	.txt-user.spacing{
		margin-right: 30px;
	}
	.record-content{
		width: 100%;
		font-size: 0;
		line-height: 0;
		letter-spacing: -4px;
	}
	.txt-record{
		font-size: 14px;
		color: #999999;
		line-height: 30px;
		font-weight: 500;
		letter-spacing: 0;
	}
	.txt-record.spacing{
		margin-right: 15px;
	}
	.sub-txt{
		font-size: 14px;
		color: #999999;
		line-height: 30px;
		font-weight: 500;
	}
	.sub-txt.spacing{
		margin-right: 10px;
	}
	.address-row{
		width: 100%;
		font-size: 0;
		line-height: 0;
		letter-spacing: -4px;
		margin: 20px 0 0;
	}
	.record-base{
		width: 575px;
		display: inline-block;
		vertical-align: middle;
	}
	.txt-base{
		font-size: 14px;
		color: #666666;
		line-height: 30px;
		font-weight: 500;
		letter-spacing: 0;
	}
	.txt-base.spacing{
		margin-right: 20px;
	}
	.btn-record{
		display: inline-block;
		width: 200px;
		height: 44px;
		background: #8BC0F0;
		font-size: 18px;
		color: #FFFFFF;
		line-height: 20px;
		text-align: center;
		font-weight: 500;
		letter-spacing: 0;
		vertical-align: middle;
		border: none;
	}
	.appointment-status{
		width: 80px;
		height: 20px;
		border-radius: 0 6px 0 6px;
		background: #0084FF;
		font-size: 14px;
		color: #FFFFFF;
		line-height: 20px;
		text-align: center;
		position: absolute;
		right: 0;
		top: 0;
		z-index: 1;
		letter-spacing: 0;
	}
</style>



