const articlenav = {
  namespaced: true,
  state: {
    data: []
  },
  getters: {
  	data(state, getters) {
        return state.data
    }
  },
  mutations: {
    ARTICLENAV(state, data) {
      state.data = [...state.data, ...data]
    }
  },
  actions: {
    updatearticlenav({commit, state},data) {
        commit('ARTICLENAV', data)
    }
  }
}
export default articlenav



