			    	<div class="nav-list-inner" :class="[{'align-center': index===list.length-1}]">
			    		<div class="nav-item" v-for="(sub, subindex) in item" :key="'navsub' + subindex" @click.stop="switchpage(index, subindex, sub.id)">
				    		<div class="nav-item-inner">
					    		<!--<div class="nav-name" :class="[{'active': curpageindex===index}]">{{item.name}}</div>
