		<cus-register :dialogvisible='registerVisible' @closedialog="closedialog"></cus-register>
		<cus-login :dialogvisible='loginVisible' @closedialog="closedialog" @loginstatus="setLoginDtatus"></cus-login>
		<cus-reset :dialogvisible='resetVisible' @closedialog="closedialog"></cus-reset>
  </div>
