<script>
import api from '../api/api'
import CusRegister from '../components/register'
import CusLogin from '../components/login'
import CusReset from '../components/reset'
import Storage from '@/util/storage'
export default {
  name: 'Navheader',
  data () {
    return {
    	isLogin: false,
    	showSub: false,
    	nickname: '',
      linkdata: [],
      sitedata: [],
    	linkdatas: [{
    		id: 1,
    		name: '网站链接',
    		url: ''
    	},{
    		id: 2,
    		name: '网站链接',
