    	},{
    		id: 6,
    		name: '网站链接',
    		url: ''
    	},{
    		id: 7,
    		name: '网站链接',
    		url: ''
    	},{
    		id: 8,
    		name: '网站链接',
    		url: ''
    	},{
    		id: 9,
    		name: '网站链接',
    		url: ''
    	},{
    		id: 10,
    		name: '网站链接',
    		url: ''
    	},{
    		id: 11,
    		name: '网站链接',
    		url: ''
    	},{
    		id: 12,
    		name: '网站链接',
    		url: ''
    	},{
    		id: 13,
    		name: '网站链接',
    		url: ''
    	},{
    		id: 14,
    		name: '网站链接',
    		url: ''
    	},{
    		id: 15,
    		name: '网站链接',
    		url: ''
    	}],
    	curpageindex: -1,
      articlenav: [], //文章列表导航
      list: [[{
      	id: 1,
      	name: '首页',
      	alias: 'index',
        son_nav: []
      }],[{
      	id: 1,
      	name: '在线估分',
      	alias: 'estimate',
        son_nav: []
      },{
      	id: 2,
      	name: '选学校',
      	alias: 'school',
        son_nav: []
      }, {
      	id: 3,
      	name: '院校专业',
      	alias: 'major',
      	son_nav: []
      },  {
      	id: 4,
      	name: '预约咨询',
      	alias: 'appointment',
          son_nav: []
      }],[],[{
      	id: 1,
      	name: '关于我们',
      	alias: 'aboutus',
        son_nav: []
      },{
      	id: 2,
      	name: '招商加盟',
      	alias: 'joinus',
        son_nav: []
      }]],
      accountlist: [{
					id: 1,
					name: '个人资料',
					alias: 'profile',//别名
					page: '/profile'
				},{
					id: 2,
					name: '估分查询记录',
					alias: 'esrecord',//别名
					page: '/estimaterecords'
				},{
					id: 3,
					name: '选学校查询记录',
					alias: 'screcord',//别名
					page: '/schoolrecords'
				},{
					id: 4,
					name: '院校专业查询记录',
					alias: 'marecord',//别名
					page: '/majorrecords'
				},{
					id: 5,
					name: '预约咨询记录',
					alias: 'aprecord',//别名
					page: '/apporecordsclient'
				},
        // {
				// 	id: 6,
				// 	name: '消息中心',
				// 	alias: 'message',//别名
				// 	page: '/messagecenter'
				// },
        {
					id: 7,
					name: '关于我们',
					alias: 'aboutus',//别名
					page: '/aboutus'
				},{
					id: 8,
					name: '设置',
					alias: 'setting',//别名
					page: '/setting'
				}],
      	registerVisible: false,
				loginVisible: false,
				resetVisible: false
    }
  },
  created() {
    let _this = this
    _this.requestlinklist()
    _this.requestsite()
  	this.requestarticlenav().then(()=>{
      _this.accountdata()
    })
  },
  watch: {
  	isLogin(val){
  		var _this = this
  		if(val){
  			_this.getCenter()
  		}
  	},
    $route(to,from){
      // console.log('from', from)
      return this.accountdata();
    }
  },
  methods: {
    siteInfo(name) {
      let cursite = this.sitedata.filter((fobj, findex)=>{
        return name === fobj.name
      })
      if(cursite.length>0){
        return cursite[0].value
      }else{
        return false
      }
    },
    requestlinklist() {
      let _this = this
      api.requestlinklist({
        location: 'top'
      }).then(response=>{
        return response.data
      }).then(res=>{
        if(res.code===1){
          _this.linkdata = res.data || [];
        }
      }).catch(err=>{
        console.log('err', err)
      })
    },
    requestsite() {
      let _this = this
      api.requestsite({
        name: 'name,logo'
      }).then(response=>{
        return response.data
      }).then(res=>{
        if(res.code===1){
          _this.sitedata = res.data || [];
        }
      }).catch(err=>{
        console.log('err', err)
      })
    },
    requestarticlenav() {
      let promise = new Promise((resolve, reject)=>{
        api.requestarticlenav().then(response=>{
          return response.data
        }).then(res=>{
          if(res.code===1){
            let data = res.data||[]
            this.$store.dispatch('articlenav/updatearticlenav', data)
            this.list[2]=data.map((obj, index)=>{
              let subdata = obj.son_nav.map((ob, ind)=>{
                let subalias = ''
                if(obj.son_nav.length>0){
                  subalias = 'questions'
                }else{
                  subalias = 'companys'
                }
                return {
                  id: ob.id,
                  name: ob.name,
                  alias: subalias
                }
              })
              let alias = ''
              if(obj.son_nav.length>0){
                alias = 'questions'
              }else{
                alias = 'companys'
              }
              return {
                id: obj.id,
                name: obj.name,
                alias: alias,
                son_nav: subdata
              }
            })
            resolve()
          }
        }).catch(err=>{})
      })
      return promise
    },
  	register() {
  		this.registerVisible = true
  		this.loginVisible = this.resetVisible = false
  	},
  	login() {
  		this.loginVisible = true
  		this.registerVisible = this.resetVisible = false
  	},
  	closedialog(data) {
    	this.registerVisible = this.loginVisible = this.resetVisible = false
  		if(data.type==='register' && !!data.nextdialog){
  			this.registerVisible = true
  		}
  		if(data.type==='login' && !!data.nextdialog){
  			this.loginVisible = true
  		}
  		if(data.type==='reset' && !!data.nextdialog){
  			this.resetVisible = true
  		}
  	},
  	setLoginDtatus() {
  		this.isLogin = true
  		this.getCenter()
  	},
    accountdata() {
      let _this = this
      // var user_id = Storage.localGet('user_id') || null
      // if(!!user_id){
      //   _this.isLogin = true
      //   _this.getCenter()
      // }else{
      //   _this.isLogin = false
      //   _this.loginVisible = true
      // }
      _this.getCenter()
    },
    getCenter() {
    	var _this = this
    	api.requestcenter().then((res) => {
				return res.data
			}).then((res) => {
				if(res.code === 1){
					_this.nickname = res.data.nickname
          this.isLogin = true
        }else if(res.code === 401){
				  this.isLogin = false
          _this.loginVisible = true
        }
			}).catch((err) => {
				console.log('err:', err)
        if(err.response.status === 401){
          this.isLogin = false
          _this.loginVisible = true
        }
			})
    },
    switchpage(index, subindex, onelevelid, twolevelid) {
      // console.log('index-----',index)
      // console.log('subindex-----', subindex)
      // console.log('onelevelid-----', onelevelid)
      // console.log('twolevelid-----', twolevelid)
   		var _this = this
      var path = null
      let id = onelevelid
      let subid = twolevelid
      if(twolevelid!==undefined){
        id = subid
      }
   		_this.curpageindex = index
   		_this.curpagesubindex = subindex
    	// _this.curpageselectorindex = !!e&&e.detail || 0;
      let options = {}
      // console.log('_this.list[index][subindex].alias---', _this.list[index][subindex].alias)
   		switch (_this.list[index][subindex].alias){
        case 'index':
          path = '/'
          break;
        case 'estimate':
          path = '/estimate'
          break;
   			case 'school':
   				path = '/school'
   				break;
   			case 'major':
   			  break;
   				/*path = '/major'
   				break;*/
   			case 'appointment':
   				path = '/appointment'
   				break;
   			case 'questions':
   				if(twolevelid!==undefined){
            path = '/questions'
            options.pid = onelevelid
            options.id = twolevelid
            options.title = encodeURIComponent(_this.list[index][subindex].name)
   				}
   				break;
        case 'companys':
          path = '/questions'
          options.pid = onelevelid
          options.title = encodeURIComponent(_this.list[index][subindex].name)
          break;
   			// case 'companys':
   			// 	path = '/companys'
   			// 	break;
   			case 'aboutus':
   				path = '/aboutus'
   				break;
   			case 'joinus':
   				path = '/joinus'
   				break;
   			default:
   				path = '/estimate'
   				break;
   		}
   		if(!!path){
   			_this.$router.push({
	   			path: path,
          query: {...options}
	   		})
   		}
   	},
   	navPage(index) {
			var _this = this
			_this.showSub = false
			_this.$router.push({path: _this.accountlist[index].page})
	 	},
    requestLogout() {
      let _this = this
      api.requestlogout().then(response=>{
        return response.data
      }).then(res=>{
        if(res.code === 1){
          _this.showSub = false
          _this.isLogin = false
          Storage.localRemove('user_id')
          _this.$message.success(res.msg)
          _this.$router.push({
            path: 'index'
          })
        }else{
          _this.$message.info(res.msg)
        }
      }).catch(err=>{
        console.log('err', err)
      })
    }
  },
  components: {
		CusRegister,
		CusLogin,
		CusReset,
  }
}
</script>

