<template>
	<div class="template-cusdialog">
		<!--左下角可以带提示，也可以不带-->
		<el-dialog class="dialog-cusdialog" :title="title" center :visible.sync="isShowDialogVisible" @close="close">
		  <div style="margin-top: -40px;">
		  	  <p class="cusdialog-msg">{{content}}</p>
		  </div>
		  <div slot="footer">
		  	  <p class="cusdialog-tip">
			  	<span>{{tips}}</span>
			  </p>
		  	  <div class="cusdialog-btns">
		  	  	<el-button v-if="showCancelButton" :class="['cus-btn', {'btn-fullwidth': showCancelButton===true && showConfirmButton===false}]" type="info" @click="close">{{canceltitle}}</el-button>
		  	  	<el-button v-if="showConfirmButton" :class="['cus-btn', {'btn-fullwidth': showCancelButton===false && showConfirmButton===true}, {'spacing': showCancelButton===true && showConfirmButton===false}]" type="primary" @click="confirm">{{confirmtitle}}</el-button>
		  	  </div>
		  </div>
		</el-dialog>
	</div>
</template>

<script>
	export default{
		props: ['custitle', 'cuscontent', 'custips','hidecancelbutton', 'hideconfirmbutton', 'cuscanceltitle', 'cusconfimtitle', 'dialogvisible'],
		created() {
			//console.log(this.hidecancelbutton && true,this.hideconfirmbutton && true)
		},
		data() {
			return{
				isShowDialogVisible: this.dialogvisible || false,
				content: this.cuscontent || '',
				tips: this.custips || '',
				title: this.custitle || '',
				canceltitle: this.cuscanceltitle || '取消',
				confirmtitle: this.cusconfimtitle || '确定',
				showCancelButton: !this.hidecancelbutton && true,
				showConfirmButton: !this.hideconfirmbutton && true
			}
		},
    watch: {
      dialogvisible(val) {
        this.isShowDialogVisible = val
        console.log(val)
      },
      cuscontent(val) {
        this.content = val
      },
      custips(val) {
        this.tips = val
      },
      custitle(val) {
        this.title = val
      },
      cuscanceltitle(val) {
        this.canceltitle = val
      },
      cusconfimtitle(val) {
        this.confirmtitle = val
      },
      hidecancelbutton(val) {
        this.showCancelButton = !val
      },
      hideconfirmbutton(val) {
        this.showConfirmButton = !val
      }
    },
		methods: {
      close() {
        this.isShowDialogVisible = false
        this.$emit('handlecancel')
      },
      confirm() {
        this.close()
        this.$emit('handleconfirm')
      }
		}
	}
</script>

<style scoped>
	.dialog-cusdialog{

	}
	.cusdialog-msg{
		font-size: 16px;
		color: #666666;
		line-height: 30px;
		text-align: center;
		font-weight: 500;
	}
	.cusdialog-tip{
		width: 100%;
		font-size: 14px;
		color: #666666;
		line-height: 30px;
		font-weight: 500;
		text-align: left;
		margin-bottom: 20px;
	}
	.cusdialog-icon-select{
		font-size: 20px;
		vertical-align: middle;
		margin-right: 5px;
		cursor: pointer;
		height: 30px;
	}
	.cusdialog-btns{
		width: 100%;
		text-align: center;
		line-height: 0;
		font-size: 0;
	}
	.cus-btn{
		width: 200px;
		height: 44px;
	}
	.cus-btn.spacing{
		margin-left: 40px;
	}
	.btn-fullwidth{
		width: 100%;
	}
</style>



