<template>
	<div class="page-estimate">
		<div class="estimate">
			<cus-mine-base curpage="估分查询记录">
				<div class="mine-content">
					<ul class="mine-view-ul" v-if="data.length>0">
						<li class="mine-view-li" v-for="item in data" :key="item.id">
							<div class="mine-view-title show-line-1">{{item.name||'在线估分记录'}}</div>
							<div class="mine-view-time">{{item.time}}</div>
							<el-button class="mine-btn" type="info" @click="toDetails(item)">详情</el-button>
						</li>
					</ul>
					<Nodatawithpic v-else></Nodatawithpic>
				</div>
			</cus-mine-base>
		</div>
	</div>
</template>

<script>
	import CusMineBase from '../components/cusminebase'
  import api from '../api/api'
	export default{
		created() {
			//this.msgboxSaveSuccess()
		},
		data() {
			return {
			  data: [],
				datas: [{
					id: 1,
					title: '在线估分查询',
					time: '2019-08-12 11：52'
				},{
					id: 2,
					title: '在线估分查询',
					time: '2019-08-12 11：52'
				},{
					id: 3,
					title: '在线估分查询',
					time: '2019-08-12 11：52'
				},{
					id: 4,
					title: '在线估分查询',
					time: '2019-08-12 11：52'
				},{
					id: 5,
					title: '在线估分查询',
					time: '2019-08-12 11：52'
				}]
			}
		},
    created() {
		  this.requestForecastRecord()
    },
		methods: {
      requestForecastRecord() {
        api.requestforecastrecord().then(response=>{
          return response.data
        }).then(res=>{
          if(res.code === 1){
            this.data = res.data||[]
          }
        }).catch(err=>{
          console.log('err', err)
        })
      },
      toDetails(item) {
        this.$router.push({name: 'Estiresult', query: {id: item.id, hasRecommend: true, type: 'estimate'}})
      }
		},
		components: {
			CusMineBase
		}
	}
</script>

<style scoped>
	.page-estimate{
		width: 100%;
	}
	.estimate{
		width: 100%;
	}
	.mine-content{
		padding: 18px 24px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.mine-view-ul{
		width: 100%;
	}
	.mine-view-li{
		width: 100%;
		min-height: 60px;
		font-size: 0;
		line-height: 0;
		letter-spacing: -4px;
		border-bottom: 1px dashed #BBBBBB;
	}
	.mine-view-li::after{
		content: '';
		display: inline-block;
		width: 0;
		height: 60px;
		vertical-align: middle;
	}
	.mine-view-title{
		display: inline-block;
		max-width: 480px;
		font-size: 18px;
		color: #666666;
		line-height: 30px;
		letter-spacing: 0;
		vertical-align: middle;
	}
	.mine-view-time{
		display: inline-block;
		max-width: 500px;
		font-size: 18px;
		color: #666666;
		line-height: 30px;
		white-space: nowrap;
		margin-left: 20px;
		letter-spacing: 0;
		vertical-align: middle;
	}
	.mine-btn{
		width: 80px;
		height: 24px;
		background: #EEEEEE;
		border-radius: 4px;
		font-size: 14px;
		color: #999999;
		line-height: 24px;
		text-align: center;
		border: none;
		padding: 0;
		display: inline-block;
		letter-spacing: 0;
		float: right;
		margin-top: 18px;
	}
</style>



