          <div class="">
            <div class="news-tabs-card side-l-form-body">
              <h3 class="side-l-form-title">预约咨询</h3>
              <div class="form-appointment">
                <div class="form-row">
                  姓名：
                  <el-input
                    class="input-appointment"
                      placeholder=""
                      v-model="form.realname">
                  </el-input>
                </div>
                <div class="form-row">
                  手机号：
                  <el-input
                    class="input-appointment"
                      placeholder=""
                      v-model="form.mobile">
                  </el-input>
                </div>
                <div class="form-row" v-if="form.subjects.length>0">
                  学科性质：
                  <el-radio-group class="checkbox-appointment" v-model="form.checkedSubjects">
                      <el-radio v-for="item in form.subjects" :label="item.alias" :key="item.id">{{item.name}}</el-radio>
                  </el-radio-group>
                </div>
                <div class="form-row">
                  总分：
                  <el-input
                    class="input-appointment"
                      placeholder=""
                      v-model="form.score">
                  </el-input>
                </div>
                <div class="form-row">
                  位次：
                  <el-input
                    class="input-appointment"
                      placeholder=""
                      v-model="form.ranking">
                  </el-input>
                </div>
                <div class="form-row">
                  语文成绩：
                  <el-input
                    class="input-appointment"
                      placeholder=""
                      v-model="form.yw">
                  </el-input>
                </div>
                <div class="form-row">
                  数学成绩：
                  <el-input
                    class="input-appointment"
                      placeholder=""
                      v-model="form.sx">
                  </el-input>
                </div>
                <div class="form-row">
                  英语成绩：
                  <el-input
                    class="input-appointment"
                      placeholder=""
                      v-model="form.yy">
                  </el-input>
                </div>
                <div class="form-row">
                  {{form.checkedSubjects==='wk'?'文':'理'}}综成绩：
                  <el-input
                    class="input-appointment"
                      placeholder=""
                      v-model="form.zh">
                  </el-input>
                </div>
                <div class="form-row" v-if="form.batchs.length>0">
                  批次：
                  <el-checkbox-group class="checkbox-appointment" v-model="form.checkedBatchs">
                      <el-checkbox v-for="item in form.batchs" :label="item.alias" :key="item.id">{{item.name}}</el-checkbox>
                  </el-checkbox-group>
                </div>
                <div class="form-row">
                  预约时间：
                  <span class="date-item">
                    <el-select class="cus-select" v-model="form.dates.year" placeholder="请选择">
                        <el-option label="选项一" value="2019"></el-option>
                        <el-option label="选项二" value="2018"></el-option>
                        <el-option label="选项三" value="2017"></el-option>
                    </el-select>
                    <span>年</span>
                  </span>
                  <span class="date-item">
                    <el-select class="cus-select" v-model="form.dates.month" placeholder="请选择">
                        <el-option label="选项一" value="2019"></el-option>
                        <el-option label="选项二" value="2018"></el-option>
                        <el-option label="选项三" value="2017"></el-option>
                    </el-select>
                    <span>月</span>
                  </span>
                  <span class="date-item">
                    <el-select class="cus-select" v-model="form.dates.day" placeholder="请选择">
                        <el-option label="选项一" value="2019"></el-option>
                        <el-option label="选项二" value="2018"></el-option>
                        <el-option label="选项三" value="2017"></el-option>
                    </el-select>
                    <span>日</span>
                  </span>
                  <span class="date-item">
                    <el-select class="cus-select" v-model="form.dates.hour" placeholder="请选择">
                        <el-option label="选项一" value="2019"></el-option>
                        <el-option label="选项二" value="2018"></el-option>
                        <el-option label="选项三" value="2017"></el-option>
                    </el-select>
                    <span>时</span>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </el-col>
        <el-col :span="7">
          <div class="ad-pic-txt">
            <h3 class="news-card-title">平台认证优质学校</h3>
            <div class="ad-pt-list">
              <div class="ad-pt-item">
                <div class="ad-pt-poster"></div>
                <p class="ad-pt-txt show-line-1">学校名简介学校名称简介学校名简介学校学校</p>
                <p class="ad-pt-extra show-line-1">此学校距您位置距离2.5km</p>
              </div>
              <div class="ad-pt-item">
                <div class="ad-pt-poster"></div>
                <p class="ad-pt-txt show-line-1">学校名简介学校名称简介学校名简介学校学校</p>
                <p class="ad-pt-extra show-line-1">此学校距您位置距离2.5km</p>
              </div>
            </div>
          </div>
          <div class="news-txt-card">
            <h3 class="news-card-title">公司动态<span class="news-tabs-more">更多<i class="el-icon-d-arrow-right news-tabs-more-icon"></i></span></h3>
            <div class="news-body">
              <div class="pt-tabs-card">
                <el-row :gutter="13">
                  <el-col :span="8">
                    <div class="pt-tabs-pic-poster"></div>
                  </el-col>
                  <el-col :span="8">
                    <div class="pt-tabs-pic-poster"></div>
                  </el-col>
                  <el-col :span="8">
                    <div class="pt-tabs-pic-poster"></div>
                  </el-col>
                </el-row>
              </div>
              <div class="news-tabs-txt-list">
                <ul class="news-tabs-ul">
                  <li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标</li>
                  <li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题标题标题标题标题标题标题标题</li>
                  <li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题</li>
                  <li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标</li>
                  <li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题题标题标题</li>
                </ul>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
			<div class="cus-error cus-padding-30" style="width: 100%;font-size: 12px;color: #F56C6C;line-height: 30px;">{{errmsg}}</div>
			<div class="appointment-btns">
				<el-button class="appointment-btn btn-submit" type="primary" @click="advisory">立即预约</el-button>
			</div>
		</div>
		<cus-dialogpay :paymoney="money" :dialogvisible="dialogPayVisible"></cus-dialogpay>
		<cus-paymethod :dialogvisible="dialogPaymethodVisible"></cus-paymethod>
	</div>
</template>

<script>
	import CusPagetitle from '../components/pagetitle'
	import CusSinglecell from '../components/singlecell'
	import CusDialogpay from '../components/cusdialogpay'
	import CusPaymethod from '../components/cuspaymethod'
	export default{
		created() {
			//this.msgboxSaveSuccess()
		},
		data() {
			return {
				form: {
					realname: '',
					mobile: '',
					score: '',
					ranking: '',
					yw: '',
					sx: '',
					yy: '',
					zh: '',
					subjects: [{
						id: 1,
						name: '文科',
						alias: 'wk'
					},{
						id: 2,
						name: '理科',
						alias: 'lk'
					}],
					checkedSubjects: 'wk',
					batchs: [{
						id: 1,
						name: '本科一批',
						alias: 'yb'
					},{
						id: 2,
						name: '本科二批',
						alias: 'eb'
					}],
					checkedBatchs: [],
					dates: {
						year: '',
						month: '',
						day: '',
						hour: ''
					}
				},
				errmsg: '',
				dialogPayVisible: false,
				money: '9.00',
				dialogPaymethodVisible: false
			}
		},
		watch: {
			form: {
				handler(val, oldval){
					this.errmsg = ''
				},
				deep: true
			}
		},
		methods: {
			selectAgreement() {
				this.isSelectedAggreement = !this.isSelectedAggreement
			},
			closedialog() {
				var _this = this
				_this.dialogPayVisible = false
			},
			checkboxchange(checked, event, index){
				var _this = this
				if(!checked){
					_this.$delete(_this.form[index].checkedForm,event.target._value.id)
				}
			},
			formvalidate() {
				var _this = this
				var promise = new Promise(function(resolve, reject){
					if(_this.form.realname !== '' && _this.form.realname !== null){
						if(_this.form.mobile !== '' && _this.form.mobile !== null){
              if(_this.form.checkedSubjects.length > 0){
                if(_this.form.score !== '' && _this.form.score !== null){
                  if(_this.form.ranking !== '' && _this.form.ranking !== null){
                    if(_this.form.yw !== '' && _this.form.yw !== null){
                      if(_this.form.sx !== '' && _this.form.sx !== null){
                        if(_this.form.yy !== '' && _this.form.yy !== null){
                          if(_this.form.zh !== '' && _this.form.zh !== null){
                            if(_this.form.checkedBatchs.length > 0){
                              if(_this.form.dates.year !== '' && _this.form.dates.year !== null && _this.form.dates.month !== '' && _this.form.dates.month !== null && _this.form.dates.day !== '' && _this.form.dates.day !== null && _this.form.dates.hour !== '' && _this.form.dates.hour !== null){
                                resolve()
                              }else{
                                _this.errmsg = '请选择时间'
                              }
                            }else{
                              _this.errmsg = '请选择批次'
                            }
                          }else{
                            _this.errmsg = '请输入' + (_this.form.checkedSubjects==='wk'?'文':'理') + '综成绩'
                          }
                        }else{
                          _this.errmsg = '请输入英语成绩'
                        }
                      }else{
                        _this.errmsg = '请输入数学成绩'
                      }
                    }else{
                      _this.errmsg = '请输入语文成绩'
                    }
                  }else{
                    _this.errmsg = '请输入位次'
                  }
                }else{
                  _this.errmsg = '请输入总分'
                }
              }else{
                _this.cusuni.custoast('请选择学科性质')
              }
						}else{
							_this.errmsg = '请输入手机号码'
						}
					}else{
						_this.errmsg = '请输入姓名'
					}
				})

				return promise
			},
			getformdata() {
				var _this = this
				var promise = new Promise(function(resolve, reject){
					_this.formvalidate().then(() => {
						var formData = {
							realname: _this.form.realname,
							mobile: _this.form.mobile,
							score: _this.form.score,
							ranking: _this.form.ranking,
							subject: _this.form.checkedSubjects,
							batch: _this.form.checkedBatchs.join(','),
							yw: _this.form.yw,
							sx: _this.form.sx,
							yy: _this.form.yy,
							zh: _this.form.zh,
							date: _this.form.dates.year + '-' + _this.form.dates.month + '-' + _this.form.dates.day + ' ' + _this.form.dates.hour
						}
						resolve(formData)
					}).catch((err) => {
						console.log('err:', err)
					})
				})

				return promise
			},
			advisory() {
				var _this = this
				_this.getformdata().then((formData) => {
					console.log('formData:', formData)
					api.requestadvisory(formData).then((res) => {
						return res.data
					}).then((res) => {
						if(res.code === 1){
							// _this.$router.push({name: 'Estiresult', query: {id: res.data.id, hasRecommend: true, type: 'school'}});
              this.$message.success(res.msg);
              _this.$router.go(-1);
						}else{
							this.$message.error(res.msg);
						}
					}).catch(err => {
						console.log('err:', err)
					})
				}).catch((err) => {
					console.log('err:', err)
				})
			}
		},
		components: {
			CusPagetitle,
			CusSinglecell,
			CusDialogpay,
			CusPaymethod
		}
	}
</script>

<style scoped>
	.page-appointment{
		width: 100%;
	}
	.appointment{
		width: 1200px;
		min-height: 800px;
		margin: 30px auto;
		background: #FFFFFF;
		border: 1px solid #EEEEEE;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.form-appointment{
		width: 100%;
		padding: 5px 20px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.form-row{
		width: 100%;
		font-size: 18px;
		color: #666666;
		line-height: 20px;
		font-weight: 500;
		margin: 25px auto;
	}
	.input-appointment{
		width: 600px;
		height: 44px;
		font-size: 16px;
		color: #666666;
		line-height: 44px;
		vertical-align: text-top;
		vertical-align: middle;
	}
	.checkbox-appointment{
		display: inline-block;
	}
	hr{
	    height: 1px;
	    background: #BFBFBF;
	}
	.appointment-btns{
		width: 100%;
		padding: 0 20px;
		box-sizing: border-box;
		font-size: 0;
		line-height: 0;
		margin-bottom: 45px;
	}
	.appointment-btn{
		width: 200px;
		height: 44px;
		border-radius: 6px;
		font-size: 18px;
		color: #FFFFFF;
		line-height: 44px;
		text-align: center;
		font-weight: 500;
		box-sizing: border-box;
		padding-top: 0;
		padding-bottom: 0;
	}
	.btn-submit{
		background: #FACD89;
		border-color: #FACD89;
		margin: 30px 0;
	}
	.btn-add{
		background: #F29B76;
		border-color: #FACD89;
	}
	.el-dialog__body{
		padding-top: 0 !important;
	}
	.date-item{
		margin-right: 10px;
	}
	.cus-select{
		width: 120px;
		margin-right: 10px;
	}
</style>



