<template>
	<div class="page-details">
		<div class="details">
			<div class="page-main">
				<div class="cus-details-head">
					<div class="cus-breadcrumb-view">
						<span class="cus-breadcrumb-lab">当前位置：</span>
						<el-breadcrumb class="cus-breadcrumb" separator-class="el-icon-arrow-right">
						  <el-breadcrumb-item class="cus-breadcrumb-item" :to="{ path: '/' }">首页</el-breadcrumb-item>
						  <el-breadcrumb-item class="cus-breadcrumb-item" v-if="isList" :to="$router.go(1)">列表页</el-breadcrumb-item>
						  <el-breadcrumb-item class="cus-breadcrumb-item">详情</el-breadcrumb-item>
						</el-breadcrumb>
					</div>
					<div class="cus-details-title-view">
						<h1 class="cus-details-title">{{!!data.data&&data.data.title||null}}</h1>
						<p class="cus-details-time">{{!!data.data&&data.createtime||null}}</p>
					</div>
				</div>
				<el-row :gutter="26">
				  <el-col :span="17">
				    <div class="pt-details-content" v-html="!!data.data&&data.data.content||null"></div>
				    <div class="page-alink-list">
              <div class="page-alink-item show-line-1" v-if="!!data.prev&&!!data.prev.id" @click="toNewsDetails(data.prev)">上一篇：{{data.prev.title}}</div>
              <div class="page-alink-item show-line-1" v-if="!!data.next&&!!data.next.id" @click="toNewsDetails(data.next)">下一篇：{{data.next.title}}</div>
              <!--<div class="page-alink-item show-line-1" @click="toNewsDetails({id: 9})">上一篇：标题标题标题{{$route.query.id}}</div>
              <div class="page-alink-item show-line-1" @click="toNewsDetails({id: 11})">下一篇：标题标题标题标题{{$route.query.id}}</div>-->
				    </div>
				  </el-col>
				  <el-col :span="7">
				    <div class="news-txt-card">
						<h3 class="news-card-title">公司动态<span class="news-tabs-more">更多<i class="el-icon-d-arrow-right news-tabs-more-icon"></i></span></h3>
						<div class="news-body">
							<div class="pt-tabs-card">
								<el-row :gutter="13">
									<el-col :span="8">
										<div class="pt-tabs-pic-poster"></div>
									</el-col>
									<el-col :span="8">
										<div class="pt-tabs-pic-poster"></div>
									</el-col>
									<el-col :span="8">
										<div class="pt-tabs-pic-poster"></div>
									</el-col>
								</el-row>
							</div>
							<div class="news-tabs-txt-list">
								<ul class="news-tabs-ul">
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题标题标题标题标题标题标题标题</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题题标题标题</li>
								</ul>
							</div>
						</div>
					</div>
				    <div class="news-txt-card">
						<h3 class="news-card-title">公司动态<span class="news-tabs-more">更多<i class="el-icon-d-arrow-right news-tabs-more-icon"></i></span></h3>
						<div class="news-body">
							<div class="pt-tabs-card">
								<el-row :gutter="13">
									<el-col :span="8">
										<div class="pt-tabs-pic-poster"></div>
									</el-col>
									<el-col :span="8">
										<div class="pt-tabs-pic-poster"></div>
									</el-col>
									<el-col :span="8">
										<div class="pt-tabs-pic-poster"></div>
									</el-col>
								</el-row>
							</div>
							<div class="news-tabs-txt-list">
								<ul class="news-tabs-ul">
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题标题标题标题标题标题标题标题</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标</li>
									<li class="news-tabs-li pt-tabs-li show-line-1">【招生访谈】标题标题标题标题标题标题标题题标题标题</li>
								</ul>
							</div>
						</div>
					</div>
				  </el-col>
				</el-row>
			</div>
		</div>
	</div>
</template>

<script>
	import api from '@/api/api'
	export default{
		data() {
			return {
        isList: false,
				data: {},
			}
		},
		created() {
		  this.start()
    },
    watch:{
      $route(to,from){
        console.log('from', from)
        switch (from.name) {
          case 'Entry': case 'Newsdetails':
            break;
          default:
            this.isList = true
            break;
        }
        return this.start();
      }
    },
    /*beforeRouteEnter(to, from, next) {
      console.log('from', from)
      console.log('to', to)
      next()
    },*/
		methods: {
		  start() {
        this.requestArticleDetails()
      },
      requestArticleDetails() {
        api.requestarticledetails({
          id: this.$route.query.id
        }).then(response=>{
          return response.data
        }).then(res=>{
          if(res.code === 1){
            this.data = res.data||{}
          }
        }).catch(err=>{
          console.log('err', err)
        })
      },
      toNewsDetails(item) {
        this.$router.push({path: '/newsdetails', query: {id: item.id}})
      }
		},
	}
</script>

<style scoped>
	.page-details{
		width: 100%;
		background: #FFFFFF;
	}
	.details{
		width: 100%;
		min-height: 800px;
		padding-bottom: 40px;
		box-sizing: border-box;
	}
	.page-main{
		width: 1200px;
		margin: 0 auto;
		padding-top: 10px;
		box-sizing: border-box;
	}
	.pt-details-content{
		width: 100%;
		font-size: 14px;
		color: #666666;
		line-height: 20px;
		margin-bottom: 45px;
	}
</style>



